import {requestUrl} from '../golbal/static.js'
let echo = function(url) {
   let query = new Object();
   query.isasync =false;
    query.beforeSend = function () {
    }
   query.url =url||requestUrl.mainUrl ;
   query.post=function () {
       query.isasync =true;
        query.ajax("POST");
   }
    query.get=function () {
        query.isasync =true;
        query.ajax("GET");
    }
    query.async=function () {
        query.isasync =false;
        query.ajax("POST");
    }
   query.data=function (data,isFormData) {
       if(!$.stringIsBlank(requestUrl.token)){
           if(isFormData){
               this.data=data
               this.echa=false
           }else {
               this.data = {data: JSON.stringify(data), token: requestUrl.token}
               this.echa=true
           }
       }else {
           this.data={data:JSON.stringify(data)}
           this.echa=true
       };
       return query;
   }
   query.loading=function (tips,type) {

       query.beforeSend=function () {
           switch (type){
               case 1:
                   this.layerIndex = layer.load(1);
                   break;
               case 2:
                   this.layerIndex = layer.msg(tips, {
                       icon: 16
                       ,shade: 0.01,
                       time:0,
                   });
                   break;
               default: this.layerIndex = layer.msg("加载中", {
                   icon: 16
                   ,shade: 0.01,
                   time:0,
               });;break;
           }
   }
       return query;
   }
    query.complete =function () {
         layer.close(this.layerIndex);
        return query;
    }
    query.err = function (func) {
        if(typeof func != undefined){
            query.error = func;
        }
        return query;
}
    query.succ = function (func) {
        if(typeof func != undefined){
            query.success = func;
        }
        return query;
   }
   query.dataFilter = function (data) {
       if(!$.stringIsBlank(data))
       data = JSON.parse(data)
       if(DEV)
       console.log(data)
      switch (data.code){
          case 200: return data.data||data.datalist||true;
          case 600:layer.msg(`${data.message}`) ;requestUrl.token=''; return false;
          default:layer.msg(data.message||"发生未知错误", {icon: 5,anim:6});return false;break;
      }
       return false;
   }
   query.ajax=function (type) {
     $.ajax(query.url,{
         type:type,
         data:this.data,
         dataType: 'text',
         success:query.success,
         error:query.error|| function(){layer.msg("发生错误，稍后再试")},
         beforeSend:query.beforeSend,
         cache: this.echa,//上传文件无需缓存
         processData: this.echa,//用于对data参数进行序列化处理 这里必须false
         contentType: this.echa?"application/x-www-form-urlencoded":false,
         complete:query.complete,
         dataFilter:query.dataFilter,
         async:query.isasync,
     })
   }
    return  query;
}

export {echo};