import {requestUrl,act} from '../golbal/static'
import router from "../plugins/router";
import {echo} from "../golbal/commons";
let requestData,tb;
let index_banner= function (d) {
    let id = d.id;
    new Vue({
        el:`.c${id}`,
        delimiters: ['${{', '}}'],
    })
    requestData={};
    requestData.act=act.bcBanner_getList;
    layui.use(['table','form'], function () {
        var table = layui.table,form=layui.form;
        tb= table.render({
            elem: `.c${id} .index-banner-management-table`
                , url: requestUrl.mainUrl
                , method: "post"
                , where: {data: JSON.stringify(requestData), token: requestUrl.token}
                , response: {
                    statusName: 'code'
                    , statusCode: 200
                    , msgName: 'message'
                    , countName: 'total'
                    , dataName: 'datalist'
                }
            , cols: [[
                {checkbox: true, fixed: true}
                , {field: 'id', title: 'ID', sort: true, fixed: true}
                ,{field:'img', title: '图片',templet:"#index-banner-management-table-img"}
                ,{field:'title', title: '标题'}
                , {field: 'url', title: '链接',}
                , {field: 'priority', title: '排序',}
                , {field: 'status', title: '状态', templet: "#index-banner-management-table-check", unresize: true}
                , {field: '', title: '操作', toolbar: '#index-banner-management-table-bar'}
            ]]

            , page: true
            , height: 'full',
        });
        table.on("tool(index-banner-management-table)",function (obj) {
            router({
                pid: obj.data,
                elem:this,
                table:tb,
            })
        })
        form.on('switch(index-banner-management-table-check)',function(obj){
            let requestData={};
            requestData.act=act.bcBanner_updateStatus;
            requestData.id=this.value;
            requestData.status=obj.elem.checked?1:0;
            echo().data(requestData).post();
        })
    });
}
export default index_banner;
let $body=$("body");
$body.on("click",".add-banner-btn",function () {
    router({
        elem:this,
        table:tb,
    })
})