Vue.component('free-shipping',{
    props:{
      shippingData:Object
    },
    template: `
<div>
   <h2-title title="包邮配置"></h2-title>
   <div class="layui-form" lay-filter="free-shipping">
     <div class="layui-form-item">
        <label class="layui-form-label">包邮范围</label>
        <div class="layui-input-inline">
        <select name="cType" lay-verify="selectthan0">
           <option  v-for="op in shippingData.options" :value="op.value" v-text="op.name" ></option>
        </select>
        </div>
    </div>
     <div class="layui-form-item">
        <label class="layui-form-label">包邮底价</label>
        <div class="layui-input-inline"><input class="layui-input" lay-verify="price" name="value" :value="shippingData.price"></div>
    </div>
     <div class="layui-form-item">
        <label class="layui-form-label"></label>
        <div class="layui-input-inline"><button class="layui-btn" lay-submit lay-filter="free-shipping-submit">提交</button></div>
    </div>
</div>
</div>
    
    `,
    mounted:function () {
        layui.use(['form'], function () {
            layui.form.render(null,'free-shipping')
        })
    }
 //vue
})