import {act} from '../golbal/static.js'
import {echo} from "../golbal/commons";
import {imgADD, putExtra, uploadConfig, uuid} from "../golbal/static";
let requsetData, isuploading=false,key='';
let index_banner_edit= function (d) {
    let id = d.id,pid=d.pid,tb=d.table;
    let data=data={data:{status:1}}
    if(pid)
        data.data=pid;
    new Vue({
        el:`.c${id}`,
        delimiters: ['${{', '}}'],
        data:data
    })

    layui.use(['form','upload'],function () {
        let form = layui.form,upload=layui.upload;

        var index_banner_photo= upload.render({
            elem: `.c${id} #index-banner-photo`
            ,auto: false
            , choose: function (obj) {
                echo().data({
                    act:act.upload_upload
                }).succ(d=>{
                    if(d){
                        let f =  $(`.c${id} #index-banner-photo-img`).parents(".layui-upload").find("input")[0].files[0]
                        let observer = {
                            next(res){
                            },
                            error(err){
                                $.tips("上传过程出现错误，稍后再试",4)
                            },
                            complete(res){
                                isuploading=false
                                layer.closeAll()
                                $(`.c${id} #index-banner-photo-img`).attr('src', `${imgADD.add}${key}${imgADD.bannerImg}`);
                            }
                        }
                        key = uuid()
                        let observable = qiniu.upload(f,key , d.uploadToken, putExtra, uploadConfig)
                        observable.subscribe(observer)
                        isuploading=true
                        $.tips(1)
                        //预读本地文件示例，不支持ie8
                        // obj.preview(function (index, file, result) {
                        //     $('#index-banner-photo-img').attr('src', result);
                        // });
                    }else {
                        isuploading=false
                    }
                }).err(()=> isuploading=false).post()
            }
        });
        // form.render(null,'index-banner-edit-form')
        form.render()
        form.on("submit(index-banner-edit-form-submit)",function (data) {
            let tips,isSucc = false;
            requsetData=data.field;
            let img = $(data.form).find(`#index-banner-photo-img`).attr('src');
            if(img){
                requsetData.img=img;
            }
            let f = $(data.form).attr("flag")=='edit'
            if(f){
                requsetData.act=act.bcBanner_update
                tips="修改";
            } else{
                tips="增加";
                requsetData.act=act.bcBanner_add
            }
            echo().data(requsetData).succ(function (d) {
                if(d){
                    isSucc = true;
                    $.tips(`${tips}成功`,3);
                    if(!f) {
                        key = '';
                        $(data.form).find(`#index-banner-photo-img`).attr('src', '');
                    }
                    tb.reload();
                }
            }).loading("上传中",2).async();
            if(f)
                return false;
            return isSucc;
        })
    })
};
export default index_banner_edit;