import {act, openModifyAdd, openRefund, openYunDan} from "../golbal/static";
import {echo} from "../golbal/commons";
import router from "../plugins/router";

let  t1,table,requestData,form,t;
let stock_order_edit =function (d) {
    layui.use(['upload', 'form', 'element', 'table','laydate'], function () {
        form = layui.form
    let id = d.id;
    t=d.table
    let vu = new Vue({
        el:`.c${id}`,
        delimiters: ['${{', '}}'],
        data:d.pid,
        methods:{
            updateAdd(){
                echo().data({
                    act:act.address_getProvinces
                }).succ(d=>{
                    if(d)
                     openModifyAdd({
                        province:d,
                        form:layui.form,
                        layFilter:'modify-add-submit'
                    })
                }).loading().post()

            },
            openYundan(){
                openYunDan({
                    form:layui.form,
                    succ: (code,no)=> {
                        echo().data({
                            act:act.bcOrder_batchDelivery,
                            orderSns:[this.orderSn],
                            code:code,
                            deliverySn:no,
                            update:1
                        }).succ(d=>{
                            if(d){
                                this.deliverySn=no
                                this.deliveryList=d.deliveryList
                            }
                        }).loading().post()
                    }
                });
            }
        }
    });


      table = layui.table;
      let cols;
      if(d.pid.orderStatus=='pendingDelivery'){
          cols=[[
              {field:'brandName', title: '品牌'}
              ,{field:'productName', title: '商品名称',width:'30%'}
              ,{field:'attr', title: '型号'}
              ,{field:'ref', title: 'REF'}
              ,{field:'num', title: '数量'}
              ,{field:'refundNum', title: '已退数量'}
              ,{field:'productPrice', title: '单价（元）'}
              ,{field:'total', title: '总价（元）'}
              ,{ title: '操作',toolbar:"#order-detail-table-bar"}
              ,
          ]]
      }else {
          cols=[[
              {field:'brandName', title: '品牌'},
              {field:'productName', title: '商品名称',width:'30%'}
              ,{field:'attr', title: '型号'}
              ,{field:'ref', title: 'REF'}
              ,{field:'num', title: '数量'}
              ,{field:'refundNum', title: '已退数量'}
              ,{field:'productPrice', title: '单价（元）'}
              ,{field:'total', title: '总价（元）'}
              ,
          ]]
      }
     t1= table.render({
          elem: `.c${id} #order-detail-table`
          ,url: ''
          ,cols: cols
          ,id: 'goods-management-list-table'
          ,page: true,
          data: d.pid.items,
      });
      
       table.on("tool(order-detail-table)",function (obj) {
           let data=obj.data;
           openRefund({
               form:form,
               num:data.num-data.refundNum,
               succ:(refund_num,refund_result)=>{
                   echo().data({
                       act:act.bcOrder_refund,
                       orderSn:vu.orderSn,
                       pid:data.productId,
                       skuId:data.skuId,
                       num:refund_num,
                       remark:refund_result
                   }).succ(d=>{
                       if(d){

                           echo().data({
                               act:act.bcOrder_getInfo,
                               orderSn:vu.orderSn
                           }).succ(d=>{
                               if(d) {
                                 t1.reload({
                                     data:d.items
                                 })
                                   $.tips('退款申请成功',3)
                               }
                           }).loading().post()
                       }
                   }).loading().post()
               }
           })
      })
        form.on("submit(modify-add-submit)",function (obj) {
                requestData=obj.field;
                requestData.orderSn=d.pid.orderSn;
                requestData.act=act.bcOrder_updateAddress
                echo().data(requestData).succ(d=>{
                    if(d) {
                        $.tips("成功", 3)
                        vu.address=obj.field
                        t.reload()
                    }
                }).post();
                return false
        })
        form.on("select(modify-add-province-city-area)",function (data) {
            let flag, $this = $(data.elem), name =  $this.attr("name");
            requestData={};
            switch (name){
                case "province":
                    requestData.act=act.address_getCitiesByProvince;
                    flag=1;
                    requestData.provinceCode=$this.find(`option[value=${data.value}]`).attr("code");
                    break;
                case "city":
                    requestData.act=act.address_getAreasByCity;
                    flag=2
                    requestData.cityCode=$this.find(`option[value=${data.value}]`).attr("code");
                    break;
                default: return false;
            }
            echo().data(requestData).succ(function (d) {
                let str="<option disabled selected>请选择</option>";
                for(let i in d){
                    str += `<option code='${d[i].code}' value="${d[i].name}">${d[i].name}</option>`
                }
                if(flag==1){
                    $this.parents(".province-city-area").find("select[name=city]").html(str);
                    $this.parents(".province-city-area").find("select[name=area]").html("<option disabled selected>请选择</option>");
                }else {
                    $this.parents(".province-city-area").find("select[name=area]").html(str);
                }

                form.render("select","modify-add-form")
            }).post();
        })
})
}

export default stock_order_edit;