import {echo} from '../golbal/commons.js'
import {requestUrl,act} from '../golbal/static.js'
let login = function(isHome) {

    let util;

    layui.use(['layer', 'util'], () => {
        util = layui.util;
    });
    let autoIndex =$.tips("自动登陆中",2)
//登录模块逻辑
    layui.use('form', () => {
        let islogin = false;
        let form = layui.form
        layer=layui.layer;
        let token =   localStorage.getItem("token");
        requestUrl.token =token;
        if(!$.stringIsBlank(token)){
            let tokendata = {
                act:act.token_validate,
                token:token,
            }

            echo().data(tokendata).succ(function (d){
                if(d){
                    $("#username span").text(d.nickname)
                    $("#username img").attr("src",d.image)
                    $("#username").attr("userId",d.id)
                    requestUrl.token = d.token;
                    requestUrl.type = d.type;
                    if(isHome)
                        $("#overview").click();
                    islogin =true;
                }else {
                    localStorage.removeItem("token");
                    islogin=false;
                }
            }).async();
        }
        layer.close(autoIndex)
        if(!islogin) {
            let i = layer.open({
                title: '登陆',
                type: 1,
                closeBtn: 0,
                skin: 'layui-layer-rim', //加上边框
                shadeClose: false,
                content: `  <div id="loginOrRegist" style="width: 360px;height: 180px">
        <login v-bind:login-Or-Regist="loginOrRegist" v-bind:code-url="codeurl"></login>
        </div>`
            });

            const COUNTDOWN = 59 * 1000;
            let loginOrRegist = new Vue({
                el: '#loginOrRegist',
                data: {
                    loginOrRegist: 'login',
                    codeurl:""
                    // loginOrRegist:'regist',
                    // loginOrRegist:'forget',
                }
            });

            form.on('submit(login)',function (data) {
                let logindata= data.field;
                logindata.act =act.login_validate;
                echo(requestUrl.mainUrl).data(logindata).succ(function (d){
                    if(d){
                        layer.close(i);
                        $("#username span").text(d.nickname)
                        $("#username img").attr("src",d.image)
                        $("#username").attr("userId",d.id)
                        requestUrl.token = d.token;
                        requestUrl.type = d.type;
                        localStorage.setItem("token", d.token);
                        if(isHome)
                            $("#overview").click();
                    }else {
                    }
                }).loading("登陆中",2).post();

                return false;
            });

        }

    });
}

$(function () {
    let $body = $("body");
    $body.on('click', '.get-code', function () {
        let telephone = $("input[name=telephone]").val();
        if ($.stringIsBlank(telephone)) {
            layer.tips('电话号码不能为空', 'input[name=telephone]', {
                tips: 3
            })
        } else {
            let $this = $(this);
            $.btnDisabled($this);
            var endTime = new Date().getTime() + COUNTDOWN, severTime = new Date().getTime();
            util.countdown(endTime, severTime, (date) => {
                let str = `${date[3]}秒后再次获取`;
                $this.html(str);
                if (date[3] == 0) {
                    $.btnEnsabled($this);
                    $this.html("获取验证码");
                }
            })
        }
    });
});
export default login;