import {echo} from '../golbal/commons.js'
import {act, requestUrl} from "../golbal/static";
let excel_export=function (d) {
    let id=d.id
  let vu=   new Vue({
        el: `.c${id}`,
        delimiters: ['${{', '}}'],
        data:{
            errors:[]
        }
    })
let form;
    layui.use([ 'form', ], function () {
        form = layui.form;
        form.render(null,'excel-form')
        form.on('submit(excel-form-submit)',function (obj) {
            // console.log(obj.form[0].files[0])
            if(!obj.field.type){
                $.tips('请选择导入类型',4)
                return false
            }
            let f= new FormData();
            f.append("data",`{"act":"${act[obj.field.type]}"}`)
            f.append("excelFile", obj.form[0].files[0]); //加入文件对象
            f.append('token',requestUrl.token)
            echo().data(f,true).succ(d=>{
                vu.errors=d
              }).loading('数据插入中',2).post()
        })

        //     console.log(f)
        //   echo().data(f,true
        //      ).succ(d=>{
        //         console.log(d)
        //   }).loading().post()
        //     return false
        // })
    })
}

export default excel_export