import {act, imgADD, putExtra, uploadConfig, uuid} from "../golbal/static";
import {echo} from "../golbal/commons";
let requestData,key1='',key2='';
let purchasing_agent_management_address=function (d) {
    let id = d.id,tb=d.table,data=d.pid;
    new Vue({
        el:`.c${id}`,
        delimiters: ['${{', '}}'],
        data:data,
        updated:function () {
            
        }
    });

    layui.use( ['form','upload'],function () {
        let form = layui.form ,upload = layui.upload;
        form.render(null, 'purchasing-agent-management-add-form');
        var  positive_photo= upload.render({
            elem: `.c${id} #positive-photo`
            ,auto: false
            , choose: function (obj) {
                echo().data({
                    act:act.upload_upload
                }).succ(d=>{
                    if(d){
                        let f =  $("#positive-photo-img").parents(".layui-upload").find("input")[0].files[0]
                        let observer = {
                            next(res){
                            },
                            error(err){
                                $.tips("上传过程出现错误，稍后再试",4)
                            },
                            complete(res){
                                isuploading=false
                                layer.closeAll()
                            }
                        }
                        key1= uuid()
                        let observable = qiniu.upload(f,key1, d.uploadToken, putExtra, uploadConfig)
                        observable.subscribe(observer)
                        isuploading=true
                        $.tips(1)
                        //预读本地文件示例，不支持ie8
                        obj.preview(function (index, file, result) {
                            $('#positive-photo-img').attr('src', result);
                        });
                    }else {
                        isuploading=false
                    }
                }).err(()=> isuploading=false).post()

            }

        });
        var  reverse_photo= upload.render({
            elem: `.c${id} #reverse-photo`
            ,auto: false
            , choose: function (obj) {
                echo().data({
                    act:act.upload_upload
                }).succ(d=>{
                    if(d){
                        let f =  $("#reverse-photo-img").parents(".layui-upload").find("input")[0].files[0]
                        let observer = {
                            next(res){
                            },
                            error(err){
                                $.tips("上传过程出现错误，稍后再试",4)
                            },
                            complete(res){
                                isuploading=false
                                layer.closeAll()
                            }
                        }
                        key2= uuid()
                        let observable = qiniu.upload(f,key2, d.uploadToken, putExtra, uploadConfig)
                        observable.subscribe(observer)
                        isuploading=true
                        $.tips(1)
                        //预读本地文件示例，不支持ie8
                        obj.preview(function (index, file, result) {
                            $('#reverse-photo-img').attr('src', result);
                        });
                    }else {
                        isuploading=false
                    }
                }).err(()=> isuploading=false).post()

            }

        });

        form.on("submit(purchasing-agent-management-form-submit)",function (data) {
            requestData=data.field;
            requestData.act=act.bcCustomer_update;
            if(key1)
            requestData.positiveImg=`${imgADD.add}${key1}${imgADD.idcard}`
            if(key2)
            requestData.oppositeImg=`${imgADD.add}${key2}${imgADD.idcard}`
            echo().data(requestData).succ(function (d) {
                if(d){
                    $.tips("修改成功",3)
                    tb.reload();
                }
            }).loading("提交中",2).post();
            return false;
        })
        form.on("submit(purchasing-agent-management-add-form-submit)",function (data) {
            requestData=data.field;
            requestData.act=act.bcAddress_update;
            echo().data(requestData).succ(function (d) {
                if(d){
                    $.tips("修改成功",3)
                }
            }).loading("提交中",2).post();
            return false;
        })
        form.on("select(province-city-area)",function (data) {
            let flag, $this = $(data.elem), name =  $this.attr("name");
            requestData={};

            switch (name){
                case "province":
                    requestData.act=act.address_getCitiesByProvince;
                    flag=1;
                    requestData.provinceCode=$this.find(`option[value=${data.value}]`).attr("code");
                    break;
                case "city":
                    requestData.act=act.address_getAreasByCity;
                    flag=2
                    requestData.cityCode=$this.find(`option[value=${data.value}]`).attr("code");
                    break;
                default: return false;
            }
            echo().data(requestData).succ(function (d) {
                let str="<option disabled selected>请选择</option>";
                for(let i in d){
                    str += `<option code='${d[i].code}' value="${d[i].name}">${d[i].name}</option>`
                }
                if(flag==1){
                    $this.parents(".province-city-area").find("select[name=city]").html(str);
                    $this.parents(".province-city-area").find("select[name=area]").html("<option disabled selected>请选择</option>");
                }else {
                    $this.parents(".province-city-area").find("select[name=area]").html(str);
                }

                form.render("select","purchasing-agent-management-add-form")
            }).post();
        })
    });


}
export default purchasing_agent_management_address;
