$(function () {
    let $body=$("body");
    $body.on("click","#logout",function () {
        layer.confirm('确定要退出登录吗?', {icon: 3, title:'提示'}, function(index){
            localStorage.removeItem("token");
            $.tips("退出成功",3);
            setTimeout(function () {
                location.reload();
            },1300)

            layer.close(index);
        });

    })
});
    export default overview

