import {echo} from '../golbal/commons.js'
import {requestUrl, act} from '../golbal/static.js'
import {imgADD, putExtra, uploadConfig, uuid} from "../golbal/static";
let  requestData={},goods_table;
//商品管理
let  editor, goodsId, goodsvue,goodsname,categoryList,brandList,isedit=false ,isuploading=false,key='',keys=[];
let form, upload, element, table, classification_of_goods_administration_bk = function (d) {
    let id = d.id;
    let data= {
        categoryList: [{name:"1",id:"1"}],
        brandList: [{name:"",id:""}]
    }
    layui.use(['upload', 'form', 'element', 'table', ], function () {
        upload = layui.upload;
        element = layui.element;
        table = layui.table;
        form = layui.form;
        let vue =   new Vue({
            el: `.c${id}`,
            delimiters: ['${{', '}}'],
            data:data,
            updated:function () {
                form.render(null,"classification-of-goods-administration-form-s-bk");

            }
        });
        echo().data({
            act: act.bcBespeakCategory_getAllList
        }).succ(function (d) {
            categoryList = d;
            vue.categoryList = categoryList;
        }).post();
        echo().data({
            act: act.bcBrand_bcGetList
        }).succ(function (d) {
            brandList = d;
            vue.brandList=brandList;
        }).post();
        requestData={};
        requestData.act = act.reBcBespeakProduct_getList;

        //商品管理列表
        goods_table=table.render({
            elem: `.c${id} #goods-management-list-table-bk`
            , url: requestUrl.mainUrl
            , method: "post"
            , where: {data: JSON.stringify(requestData), token: requestUrl.token}
            , response: {
                statusName: 'code'
                , statusCode: 200
                , msgName: 'message'
                , countName: 'total'
                , dataName: 'datalist'
            }
            , cols: [[
                {field: 'id', title: 'ID', sort: true, align: "center"}
                , {field: 'goodIcon', title: '图片', templet: "#goods-management-list-img-bk", align: "center"}
                , {field: 'name', title: '商品名称', align: "center"}
                , {field: 'brandName', title: '品牌', align: "center"}
                , {field: '', title: '状态', sort: true, templet: "#goods-management-list-check-bk", align: "center"}
                , {field: '', title: '操作', toolbar: '#goods-management-list-table-bar-bk'}
            ]]
            , page: true,
        });

        element.on('tab(classification-of-goods-administration-bar-bk)', function (data) {
            if (data.index == 1&&isedit) {
                let requestData = {};
                requestData.act = act.bcTemplate_getAllList;
                echo().data(requestData).succ(function (d) {
                    goodsvue.data = d;
                }).post();
            }
        });
        form.on('submit(classification-of-goods-administration-form-s-submit-bk)',function (data) {
            requestData={}
            requestData=data.field;
            requestData.act= act.reBcBespeakProduct_getList;
            if( data.field.status1&&data.field.status2){
                requestData.status=-1;
            }else {
                requestData.status = data.field.status1||data.field.status2;
            }
            goods_table.reload({
                where: {
                    data: JSON.stringify(requestData), token: requestUrl.token,
                }
                ,page: {
                    curr: 1 //重新从第 1 页开始
                }
            })
        })
        //监听上下架
        form.on('switch(goods-management-list-check-bk)',function(obj){
            let requestData={};
            requestData.act=act.reBcBespeakProduct_updateStatus;
            requestData.id=this.value;
            requestData.status=obj.elem.checked?1:0;
            echo().data(requestData).post();
        })

        form.on('submit(sku-template-form-submit-bk)',function (data) {
            let requestdata ={};
            if(isedit){
                requestdata.act=act.reBcBespeakProduct_updateSku;
                requestdata.pid = goodsvue.info.id;
            }else {
                requestdata.act = act.reBcBespeakProduct_addSku;
                requestdata.pid = goodsId;
            }
            requestdata.model=data.field.model;
            let skudata,list=[],skuTd= $(".sku-table").find(".sku-td");
            let uploadImg= false
            skuTd.each(function (i,e) {
                skudata={};
                skudata.id=$(e).find("input[name=id]").val();
                skudata.attr=$(e).find("input[name=attr]").val();
                skudata.ref=$(e).find("input[name=ref]").val();
                skudata.stock=$(e).find("input[name=stock]").val();
                skudata.price=$(e).find("input[name=price]").val();
                skudata.dutyFreePrice=$(e).find("input[name=dutyFreePrice]").val();
                skudata.costPrice=$(e).find("input[name=costPrice]").val();
                skudata.limitTime=$(e).find("input[name=limitTime]").val();
                skudata.limitNum=$(e).find("input[name=limitNum]").val();
                skudata.minPurchaseNum=$(e).find("input[name=minPurchaseNum]").val();
                skudata.status=$(e).find("input[name=status]:checked").val()||0;
                skudata.img=$(e).find("input[name=sku-img]").val()
                skudata.detailImg=$(e).find("input[name=sku-detail-img]").val()
                if(skudata.img=='true'){
                    skudata.img=''
                    uploadImg=true
                }
                if(skudata.detailImg=='true'){
                    skudata.detailImg=''
                    uploadImg=true
                }
                list.push(skudata);
            })
            if(uploadImg){
                $.tips('sku图片上传中，请等待上传完成后再提交',3)
                return false;
            }
            if(list.length<1){
                $.tips("请添加sku",4)
                return false;
            }
            requestdata.list=list;
            echo().data(requestdata).succ(function (d) {
                if(d) {
                    if (isedit) {
                        $.tips("修改成功", 3);
                    } else {
                        $.tips("添加成功", 3);
                        element.tabChange('classification-of-goods-administration-bar-bk', 'classification-of-goods-administration-info-bk');
                        goodsvue.skuData = []
                    }
                }
            }).loading("提交中",2).post();
        })
        form.on('submit(product-edit-bk)', function (data) {
            let a;
            if(isedit){
                a = act.reBcBespeakProduct_updateProduct
            }else {
                a = act.reBcBespeakProduct_addProduct
            }
            let goodsdata = data.field;
            goodsname = data.field.name
            goodsdata.info = editor.txt.html();
            goodsdata.act = a;
            goodsdata.categoryIds=[];
            goodsdata.goodIcon = goodsvue.info.goodIcon;
            $(".cloned .layui-form[lay-filter=classification-of-goods-form-bk]").find("input[name=categoryIds]:checked").each(function (i,e) {
                goodsdata.categoryIds.push(Number.parseInt($(e).val()))
            });
            if(goodsdata.categoryIds.length<1){
                $.tips("至少选择一个分类",4)
                return false;
            }
            let isSucc = false;
            echo().data(goodsdata).succ(function (d) {
                if (d) {
                    isSucc = true;
                    goodsId = d.id;
                    goodsvue.goodsname=goodsname;
                    if(isedit){
                        $.tips("修改成功", 3);
                    }else{
                        $('#goods-main-img-bk').attr('src', '');
                        key=''
                        $.tips("添加成功", 3);
                        goodsvue.info={}
                    }
                    if(!isedit)
                        editor.txt.html('')
                    goods_table.reload();
                    let requestData = {};
                    requestData.act = act.bcTemplate_getAllList;
                    echo().data(requestData).succ(function (d) {
                        goodsvue.data = d;
                        if(!isedit)
                            element.tabChange('classification-of-goods-administration-bar-bk', 'classification-of-goods-administration-model-bk');
                    }).post();
                } else {
                }
            }).loading("上传中", 2).async();
            if(isSucc){
                if(!isedit){
                    goodsvue.info={distributionFee:0}
                    return true;
                }
            }
            goodsvue.info.categoryIds= goodsdata.categoryIds;
            goodsvue.info.brandId= goodsdata.brandId;
            goodsvue.info.hotLevel= goodsdata.hotLevel;
            return false;
        });
        //编辑监听
        table.on('tool(goods-management-list-table-bk)',function (obj) {
            requestData={};
            requestData.act=act.reBcBespeakProduct_getInfo;
            requestData.id=obj.data.id;
            echo().data(requestData).succ(function (d) {
                open_edit_goods_bk(d);
            }).post();
        })
        form.render(null,"classification-of-goods-administration-form-s-bk");
    })

}


let $body = $("body");
$body.on("click", ".clone .add-goods-bk", function () {
    open_edit_goods_bk();
})
export default classification_of_goods_administration_bk
let open_edit_goods_bk = function (d) {

    let lid = new Date().getTime();
    let str = $(".classification-of-goods-administration-bk-layer-div-clone").clone().find(".clone").addClass("cloned").addClass(`l${lid}`).end().html();

    var index = layer.open({
        content: str,
        title: "商品管理",
        btn: [],
        type: 1,
        area:["100%","100%"]
    });
    layer.full(index)
    form.render();
    let data ={
        data: {},
        list:[],
        info:{distributionFee:0},
        specialList:{},
        categorys:[],
        brandList:[],
        goodsname:'',
        skuData:[],
        model:{name:""},
        hotLevels:[{
            name:"置顶1",value:1
        },
            {
                name:"置顶2",value:2
            },{
                name:"置顶3",value:3,
                status:1
            },{
                name:"热门1",value:4
            },{
                name:"热门2",value:5
            },{
                name:"热门3",value:6
            },
        ],
        goods_edit_btn:"提交进入下一步"
    };
    if(d){
        data.info=d.info;
        let p = d.skuList;
        for(let i in p){
            if (p[i].limitTime==0) continue;
            p[i].limitTimelimitNum=`${(function (data) {
                switch (data){
                    case 1:return "每周";
                    case 2:return "每月";
                    case 3:return "每季";
                    case 4:return "每年";
                    default:return '';
                }
            })(p[i].limitTime)}限购${p[i].limitNum}件`
        }
        for(let n in d.skuList){
            d.skuList[n].percent=0
            d.skuList[n].DetialPercent=0
        }
        data.skuData=d.skuList;
        data.model.name=d.model.name;
        data.goods_edit_btn="提交修改"
        $(".cloned .sku-title-bk").removeClass("layui-hide");
        isedit=true;
    }else {
        isedit=false;
    }
    data.data=data.data||{};
    echo().data({
        act: act.bcBespeakCategory_getAllList
    }).succ(function (d) {
        data.categorys=d;
        goodsvue = new Vue({
            el: `.l${lid}`,
            delimiters: ['${{', '}}'],
            data: data,
            updated: function () {
                form.render(null,"classification-of-goods-form-bk");
                form.render(null,"sku-template-form-bk");
            },
            computed:{
                categoryList:function () {
                    let categories = this.categorys;
                    if(this.info.categoryIds) {
                        for (let i in categories) {
                            for (let n in this.info.categoryIds) {
                                if (categories[i].id == this.info.categoryIds[n]) {
                                    categories[i].status = 1;
                                }
                            }
                        }
                    }
                    return categories;

                },
                brandListSelect:function () {
                    let brands = this.brandList;
                    if(this.info.brandId){
                        for(let i in brands){
                            if(brands[i].id == this.info.brandId){
                                brands[i].status=1;
                            }
                        }
                    }
                    return brands;
                },
                hotLevelSelect:function () {
                    let hotLevelList = this.hotLevels;
                    let hasLeve=false
                    if(this.info.hotLevel){
                        for(let i in hotLevelList){
                            hotLevelList[i].status=0;
                            if(hotLevelList[i].value == this.info.hotLevel){
                                hotLevelList[i].status=1;
                                hasLeve=true
                            }
                        }
                    }
                    if(!hasLeve){
                        hotLevelList[2].status=1
                    }
                    return hotLevelList;
                }
            },

        });


        // 富文本编辑器
        editor = new window.wangEditor(`.l${lid} .editor`);
        editor.customConfig.menus = [
            'head',  // 标题
            'bold',  // 粗体
            'fontSize',  // 字号
            'italic',  // 斜体
            'underline',  // 下划线
            'strikeThrough',  // 删除线
            'foreColor',  // 文字颜色
            'backColor',  // 背景颜色
            'list',  // 列表
            'justify',  // 对齐方式
            'quote',  // 引用
            'image',  // 插入图片
            'table',  // 表格
            'undo',  // 撤销
            'redo'  // 重复
        ]
        editor.create();
        editor.txt.html(data.info.info)
        echo().data({
            act: act.bcBrand_bcGetList
        }).succ(function (d) {
            goodsvue.brandList =d;
        }).post();

        var goods_main_img = upload.render({
            type: 1,
            elem: `.l${lid} #goods-main-img-bk`
            ,auto: false
            , choose: function (obj) {
                let index;
                var files = obj.pushFile();
                echo().data({
                    act:act.upload_upload
                }).succ(d=>{
                    if(d){
                        let f =  $(`.l${lid} #goods-main-img-view-bk`).parents(".layui-upload").find("input")[0].files[0]
                        let observer = {
                            next(res){
                            },
                            error(err){
                                $.tips("上传过程出现错误，稍后再试",4)
                            },
                            complete(res){
                                isuploading=false
                                layer.close(index)
                                goodsvue.info.goodIcon= `${imgADD.add}${key}${imgADD.productImg}`;
                            }
                        }
                        key = uuid()
                        let observable = qiniu.upload(f,key , d.uploadToken, putExtra, uploadConfig)
                        observable.subscribe(observer)
                        isuploading=true
                        index=  $.tips(1)
                        //预读本地文件示例，不支持ie8
                        obj.preview(function (index, file, result) {
                            $(`.l${lid} #goods-main-img-view-bk`).attr('src', result);
                        });
                    }else {
                        isuploading=false
                    }
                }).err(()=> isuploading=false).post()

            }

        });

        //多图片上传
        let goods_imgs = upload.render({
            elem: `.l${lid} #goods-imgs`
            , url: '/upload/'
            , multiple: true
            , before: function (obj) {
                var files = this.files = obj.pushFile();
                //预读本地文件示例，不支持ie8
                obj.preview(function (index, file, result) {
                    $(`.l${lid} #goods-imgs-views`).append('<img src="' + result + '" alt="' + file.name + '" class="layui-upload-img img-cancle">')
                    $(".img-cancle").click(function () {
                        delete files[index];
                        goods_imgs.config.elem.next()[0].value = '';
                        $(this).remove();
                    });
                });
            }
            , done: function (res) {
                //上传完毕
            }
        });
    }).post();

}


