//依赖项不要删除
import indexcss from './css/index.scss';
import plugins from  './js/plugins/jquery-plugins.js';
import login from './js/login/login.js';
import router from "./js/plugins/router";
import sku_table_bk from './js/component/sku-table-bk'
import spacial_offer from './js/component/special-offer'
import free_shipping from './js/component/free-shipping'
import spacial_offer_edit from './js/component/special-offer-edit'
import classification_of_goods_classification from './js/component/classification-of-goods-classification'
import delivery_management from './js/component/deliveryManagement'
import delivery_management_detail from './js/component/deliverMangementDetail'

$(function () {

    layui.use('element', function() {
        login(true);
        var element = layui.element;
        element.on('tabDelete', function(data){
          let id = $(this).parent().attr("lay-id");
        $(`.site-active[tag-id=${id}]`).attr("flag","off");
        $(`.site-active-edit[tag-id=${id}]`).attr("flag","off");
        });
        $("body").on("click",".site-active",function () {
            var othis = $(this);
            router({
                elem:othis
            })
        });
    })

});




