import {echo} from "../golbal/commons";
import {act, openYunDan} from "../golbal/static";


Vue.component('delivery-management-detail',{
    props: {
        data:Object
    },
    delimiters: ['*{{', '}}'],
    template: `<div>
     <h2-title title="订单查看"></h2-title>
                <div class="layui-row layui-col-space10">
                    <div class="layui-col-xs6">
                        <div class="">
                            <table class="layui-table">
                                <thead>
                                <tr>
                                    <th colspan="2">订单编号*{{data.orderSn}}</th>
                                </tr>
                                </thead>
                                <tr>
                                    <td>子订单编号</td>
                                    <td>*{{data.splitOrderSn}}</td>
                                </tr>
                                <tr>
                                    <td>订单状态</td>
                                    <td>*{{data.orderStatus}}</td>
                                </tr>
                                <tr>
                                    <td>运费</td>
                                    <td>*{{data.distributionFee}}元</td>
                                </tr>

                            </table>
                        </div>
                    </div>

                    <div class="layui-col-xs6">
                        <div class="">
                            <table class="layui-table">
                                <thead>
                                <tr>
                                    <th colspan="2">客户明细</th>
                                </tr>
                                </thead>
                                <tr>
                                    <td>姓名</td>
                                    <td>*{{data.nickname}}</td>
                                </tr>
                                <tr>
                                    <td>EMAILL</td>
                                    <td>*{{data.email}}</td>
                                </tr>
                                <tr>
                                    <td>电话</td>
                                    <td>*{{data.phone}}</td>
                                </tr>
                            </table>
                        </div>
                    </div>
                </div>
                <h2-title title="订单资料"></h2-title>
                <div class="padding15" >
                    <div class="layui-row shipping-address-title">配送地址</div>
                    <div class="layui-row shipping-address-content">*{{data.address.receiverName}}（*{{data.address.receiverPhone}}） *{{data.address.province}} *{{data.address.city}} *{{data.address.area}}*{{data.address.address}}</div>
                 

                    <table class="layui-table" id="delivery-management-detail-table" lay-filter="delivery-management-detail-table">

                    </table>
                </div>
                 <div v-if="data.orderStatus != 'waitforpay'&&data.orderStatus != 'cancel'">
                <h2-title title="物流信息" ></h2-title>
                <div class="layui-form">
                    <div class="layui-form-item">
                        <label class="layui-form-label">运单号 </label>
                        <div class="layui-input-inline"><div class="layui-form-mid">*{{data.company}}&nbsp;&nbsp;*{{data.deliverySn}}</div></div>
                        <button class="layui-btn" @click="openYundan"> *{{data.deliverySn?'修改':'添加'}}</button>
                    </div>
                    <ul class="layui-timeline">
                        <li class="layui-timeline-item" v-if="data.deliveryList.length<1 ">
                            <i class="layui-icon layui-timeline-axis">&#xe63f;</i>
                            <div class="layui-timeline-content layui-text">
                                <h3 class="layui-timeline-title"></h3>
                                <p>
                                    暂无物流信息
                                </p>
                            </div>
                        </li>
                        <li class="layui-timeline-item" v-for="h in data.deliveryList ">
                            <i class="layui-icon layui-timeline-axis">&#xe63f;</i>
                            <div class="layui-timeline-content layui-text">
                                <h3 class="layui-timeline-title">*{{h.time}}</h3>
                                <p>
                                    *{{h.context}}
                                </p>
                            </div>
                        </li>

                    </ul>
                </div>
                 </div>
</div>
    `,
methods:{
        openYundan(){
            openYunDan({
                form:layui.form,
                splitOrderSn:this.data.splitOrderSn,
                yundanInfoAct:act.bcSplitOrder_getDelivery,
                succ: (code,no)=> {
                    echo().data({
                        act:act.bcSplitOrder_delivery,
                        splitOrderSn:this.data.splitOrderSn,
                        code:code,
                        deliverySn:no,
                        update:1
                    }).succ(d=>{
                        if(d){
                            this.data.deliverySn=no
                            this.data.deliveryList=d.deliveryList
                        }
                    }).loading().post()
                }
            });
        }
},
    mounted:function () {
        layui.table.render({
            elem: `#delivery-management-detail-table`
            ,url: ''
            ,cols:[[
                {field:'brandName', title: '品牌'},
                {field:'productName', title: '商品名称',width:'30%'}
                ,{field:'attr', title: '型号'}
                ,{field:'ref', title: 'REF'}
                ,{field:'num', title: '数量'}
                ,{field:'productPrice', title: '单价（元）'}
                ,
            ]]
            ,page: true,
            data:this.data.items,
        });
    }
})