import router from "../plugins/router";
import {act} from "../golbal/static";
import {echo} from "../golbal/commons";

let  form,requestData,table, t,t1;
let distributive_buyer =function (d) {
    let id = d.id;
    t=d.table
    layui.use(['upload', 'form', 'element', 'table','laydate'], function () {
        form = layui.form;
        table=layui.table;
   let vu= new Vue({
        el: `.c${id}`,
        delimiters: ['${{', '}}'],
        data:d.pid,
        computed:{
            orderStatu(){
                switch (this.orderStatus){
                    case 'takingOrder': return '已接单';
                    case 'pengdingBuy': return '抢购中';
                    case 'waitconfirm': return '待确认';
                    case 'allReject': return '全部驳回';
                    case 'noGoods': return '无货';
                }
            }
        }
    });
        table.render({
            elem: `.c${id} #distributive-buyer-items-table`
            ,cols: [[
                {field:'productName', title: '商品名称'}
                ,{field:'attr', title: '型号'}
                ,{field:'num', title: '数量'}
                ,{field:'productPrice', title: '单价（元）'}
                ,{field:'total', title: '总价（元）'}

            ]]
            ,page: true,
            data: d.pid.items
        });
       t1 =  table.render({
            elem: `.c${id} #distributive-buyer-table`
            ,cols: [[
                {field:'nickname', title: '买手姓名'}
                ,{field:'createTime', title: '接单时间'}
                ,{field:'updateTime', title: '操作时间'}
                ,{field:'status', title: '状态'}
                ,{title: '操作',toolbar:"#distributive-buyer-bar"}

            ]]
            ,page: true,
            data: d.pid.buyerList
        });



        table.on('tool(distributive-buyer)',function (obj) {

            let data = obj.data;
            requestData={
                act:act.bcBespeakOrder_assign,
                orderSn:vu.orderSn,
                buyerId:data.id
            };
            echo().data(requestData).succ(s=>{
                if(s) {
                   t.reload()
                    vu.orderStatus='pengdingBuy'
                    echo().data({
                        act:act.bcBespeakOrder_getAssignOrderInfo,
                        orderSn:vu.orderSn,
                    }).succ(s=>{
                        if(s){
                          d.pid=s
                            t1.reload({
                                data:s.buyerList
                            })
                        }
                    }).loading().post()
                }
            }).loading().post()
        })
    });
}
export default distributive_buyer;
