//现货订单管理
import {act, openTable, openYunDan, requestUrl} from "../golbal/static";
import {echo} from "../golbal/commons";
import router from "../plugins/router";

let  orderlist,table,t,requestData,searchStatus;
let stock_order_list =function (d) {
    let id = d.id;
    new Vue({
        el:`.c${id}`,
        delimiters: ['${{', '}}'],
    });

    layui.use(['upload', 'form', 'element', 'table','laydate'], function () {
        let laydate = layui.laydate,form = layui.form;
        form.render(null,'stock-order-search-form');
        laydate.render({
            elem: `.c${id} .order-start` ,type:"datetime"}
        )
        laydate.render({
            elem: `.c${id} .order-end` ,type:"datetime"
        });
        requestData={
            act:act.bcOrder_getList
        }
      table = layui.table;
         t = table.render({
            elem: `.c${id} #stock-order-list-table`
             , url: requestUrl.mainUrl
             , method: "post"
             , where: {data: JSON.stringify(requestData), token: requestUrl.token}
             , response: {
                 statusName: 'code'
                 , statusCode: 200
                 , msgName: 'message'
                 , countName: 'total'
                 , dataName: 'datalist'
             }
            ,cols: [[
                 {checkbox: true, fixed: true},
                {field:'orderSn', title: '订单号', sort: true, fixed: true}
                ,{field:'phone', title: '客户手机'}
                ,{field:'orderStatus', title: '状态' }
                ,{field:'', title: '商品清单',toolbar:"#stock-order-list-table-items"}
                ,{field:'address', title: '地址'}
                ,{field:'', title: '操作', width:270, toolbar: '#stock-order-list-table-bar1'}

            ]],
              id: `.c${id}stock-order-list-table`
            ,page: true,

        });
         form.on("submit(stock-order-search-form-submit)",function (obj) {
             searchStatus=obj.field.orderStatus
             requestData=obj.field;
             requestData.act=act.bcOrder_getList
             t.reload( {
                 where: {
                     data: JSON.stringify(requestData), token: requestUrl.token,
                 }
                 ,page: {
                     curr: 1
                 }
             })
         })
         form.on("submit(waybill-number-submit)",function (obj) {
             if(searchStatus=='pendingDelivery'||searchStatus=='delivered'||searchStatus=='complete'){
                 let checkStatus = table.checkStatus(`.c${id}stock-order-list-table` )
                     ,data = checkStatus.data;
                 if(data.length<1){
                     $.tips('请至少选择一个',4)
                 }else {
                     let orderSns=[];
                     for(let i in data){
                         orderSns.push(data[i].orderSn)
                     }
                     openYunDan({
                         form:form,
                         succ:function (code,no) {
                             echo().data({
                                 act:act.bcOrder_batchDelivery,
                                 orderSns:orderSns,
                                 code:code,
                                 deliverySn:no,
                             }).succ(d=>{
                                 if(d)
                                     t.reload()
                             }).loading().post()
                         }
                     });
                 }
             }else {
                 $.tips('请筛选订单后再操作',4)
             }
                return false
         })
         table.on("tool(stock-order-list-table)",function (obj) {
             let layEvent = obj.event,data = obj.data;
             switch (layEvent){
                 case "order-detail":
                     openTable(data.items)
                     break;
                 case "waybill-number":
                         openYunDan({
                             form:form,
                             orderSn:data.orderSn,
                             yundanInfoAct:act.bcOrder_getDelivery,
                             succ:function (code,no) {
                                 echo().data({
                                     act:act.bcOrder_batchDelivery,
                                     orderSns:[data.orderSn],
                                     code:code,
                                     deliverySn:no,
                                 }).succ(d=>{
                                     if(d)
                                         t.reload()
                                 }).loading().post()
                             }
                         });
                     // layer.prompt({
                     //     formType:0,
                     //     title:"请输入运单号",
                     // },function (v,i,e) {
                     //     layer.close(i);
                     // })
                     break;
                 case "refund":   layer.prompt({
                     formType:0,
                     title:"请输入退款原因",
                 },function (v,i,e) {
                     layer.close(i);
                 })
                     break;
                 case "cancel":
                     layer.prompt({
                         formType:0,
                         title:"请输入取消原因",
                     },function (v,i,e) {
                         requestData={
                             act:act.bcOrder_cancel,
                             orderSn:data.orderSn,
                             remark:v
                         }
                         echo().data(requestData).succ(d=>{
                             if (d){
                                 t.reload()
                             }
                         }).loading().post();
                         layer.close(i);
                     })
                     break;
                 case 'edit':
                     requestData={
                         act:act.bcOrder_getInfo,
                         orderSn:data.orderSn
                     };
                     echo().data(requestData).succ(d=>{
                         if(d) {
                             router({
                                 pid: d,
                                 elem: this,
                                 table: t,
                             })
                         }
                     }).loading().post()
                     break;

             }

         })
})
}
