import {requestUrl,act} from "../golbal/static";
import {echo} from "../golbal/commons";

let requestData;
let admin_edit= function (d) {
    let data={
        data:{
            status:1
        },
        actType:{},
        type:requestUrl.type
    }, id = d.id,pid=d.pid,tb=d.table,type=d.type;
    data.actType=type;
    if(pid){
       data.data=pid;
    }
    new Vue({
        el:`.c${id}`,
        delimiters: ['${{', '}}'],
        data:data
    })

    layui.use(['form'],function () {
        let form = layui.form;

        form.render(null,'admin-edit-form')
        form.on("submit(admin-edit-form-submit)",function (data) {
            if(type=='add'){
                let ps=[]
                $(".admin-edit-form input[name=password]").each(function (i,e) {
                        ps.push($(e).val())
                });
                if(ps[0]!=ps[1]){
                    $.tips("两次输入的密码不一样！",4)
                    return
                }
            }
            let tips;
            requestData = data.field;
            if(pid){
                requestData.act=act.bcPurchase_update;
                tips="修改";
            }else {
                requestData.act=act.bcPurchase_add;;
                tips="添加";
            }
            let isSucc = false;
            echo().data(requestData).succ(function (d) {
                if(d){
                    isSucc = true;
                    $.tips(`${tips}成功`,3);
                    tb.reload();
                }
            }).loading("上传中",2).async();
            if(pid)
                return false;
            return isSucc;
        })
        });
    };
export default admin_edit;