import {echo} from "../golbal/commons";
import {act} from "../golbal/static";


Vue.component('delivery-management',{
    props: {

    },
    delimiters: ['${{', '}}'],
    template: ` <div> <h2-title title="发货管理"></h2-title>
     <div class="layui-collapse">
                    <div class="layui-colla-item">
                        <h2 class="layui-colla-title">筛选条件</h2>
                        <div class="layui-colla-content layui-show">
                            <div class="layui-form" lay-filter="delivery-management-form">
                                <div class="layui-inline">   <input type="text" name="orderSn" placeholder="主订单号" autocomplete="off" class="layui-input"></div>
                                <div class="layui-inline">   <input type="text" name="SplitOrderSn" placeholder="子订单号" autocomplete="off" class="layui-input"></div>
                                <div class="layui-inline">  <input type="text" name="phone" placeholder="客户手机" autocomplete="off" class="layui-input"></div>
                                <div class="layui-inline">  <select name="region" >
                                    <option value="">请选择</option>
                                    <option v-for="a in add" :value="a.name" v-text="a.name"></option>
                            
                                </select></div>
                                <div class="layui-inline">   <select name="orderStatus" >
                                    <option value="">订单状态</option>
                                    <option value="">全部</option>
                                    <option value="pendingDelivery">待发货</option>
                                    <option value="delivered">已发货</option>
                                    <option value="complete">已完成</option>
                                    <option value="refused">拒签</option>
                            
                                </select></div>
                                <div class="layui-inline"><button class="layui-btn" lay-submit  lay-filter="delivery-management-form-submit">筛选</button></div>
                           
                            </div>
                        </div>
                <table class="layui-hide" id="delivery-management-table" lay-filter="delivery-management-table" ></table>
 
               </div>
        </div>
     
        <script type="text/html" id="delivery-management-table-bar">
    {{# if(d.orderStatus == '待发货'||d.orderStatus == '已发货'||d.orderStatus == '已完成'){ }}
    <a class="layui-btn layui-btn-xs"  lay-event="waybill-number">运单号</a>
    {{# } }}

    <a class="layui-btn layui-btn-xs site-active-edit"  data-clone="delivery-management-detail-clone" bar-type="delivery_management_detail" lay-event="edit" btn-type="edit">查看/编辑</a>

    {{# if(d.orderStatus == '待支付'){ }}
    <a class="layui-btn layui-btn-xs"  lay-event="cancel">取消</a>
    {{# } }}
    {{# if(d.orderStatus == '待接单'){ }}
    <a class="layui-btn layui-btn-xs"  lay-event="takingOrder">接单</a>
    {{# } }}
    {{# if(d.orderStatus == '已接单'){ }}
    <a class="layui-btn layui-btn-xs"  lay-event="replaceOrder">代下单</a>
    {{# } }}
    {{# if(d.orderStatus == '待确认'){ }}
    <a class="layui-btn layui-btn-xs"  lay-event="confirm">确认</a>
    {{# } }}
    {{# if(d.orderStatus == '全部驳回'){ }}
    <a class="layui-btn layui-btn-xs"  lay-event="nogoods">无货</a>
    {{# } }}
    {{# if(d.orderStatus == '抢购中'||d.orderStatus == '已接单'||d.orderStatus == '待购买'||d.orderStatus == '待确认'||d.orderStatus == '无货'||d.orderStatus == '全部驳回'){ }}
    <a class="layui-btn layui-btn-xs site-active-edit" data-clone="distributive-buyer-clone" bar-type="distributive-buyer"  lay-event="distributive-buyer">分配买手</a>
    {{# } }}
</script>
     </div>
    `,
    data(){
        return {
            add: []
        }
    },
    mounted:function () {
        echo().data({
            act:act.reConfig_getRegionList
        }).succ(d=>{
            if(d)
              this.add=d
        }).post()
    },
    updated:function(){
        layui.use(['form'], function () {
            layui.form.render(null,'delivery-management-form')
        } )
    }
})