import {act, config} from "../golbal/static";
import {echo} from "../golbal/commons";

let requestData;
let free_shipping=function (d) {

    let data={
        data:{
            options:[{
                name:'一件代发',
                value:1,
            },
                {
                    name:'预约爆款',
                    value:2,
                },
                {
                    name:'全部',
                    value:3,
                }],
            price:99999
        }
    };
    requestData={};
    requestData.act=act.reConfig_getInfo;
    requestData.clazz=config.postage;

    echo().data(requestData).succ(function (p) {
       if(p){
           if(p.value) {
               data.data.price = p.value;
           }
           let t= data.data.options.splice(p.cType-1,1)
           if(t[0]) {
               data.data.options.unshift(t[0])
           }
       }

        let id = d.id;
        let vue = new Vue({
            el: `.c${id}`,
            delimiters: ['${{', '}}'],
            data:data,
            mounted:function () {

            }
        })
    }).loading().post();


    layui.use(['table' ,'form'], function () {
        layui.form.on("submit(free-shipping-submit)",function (data) {

            requestData=data.field;
            requestData.act=act.reConfig_doSubmit;
            requestData.clazz=config.postage;
           echo().data(requestData).succ(function (d) {
               if(d)
                   $.tips("设置成功",3)
           }).loading().post()
        })
    })
};

export default free_shipping;