import {requestUrl,act} from "../golbal/static";
import router from "../plugins/router";
import {echo} from "../golbal/commons";

let requestData,tb;
let purchasing_agent_management=function (d) {
    let id = d.id;
    new Vue({
        el:`.c${id}`,
        delimiters: ['${{', '}}'],
    });
    layui.use(['table','form'], function () {
        var table = layui.table,form = layui.form;
        requestData={};
        requestData.act = act.bcCustomer_getList;
        tb=table.render({
            elem: `.c${id} .purchasing-agent-management-table`
            , url: requestUrl.mainUrl
            , method: "post"
            , where: {data: JSON.stringify(requestData), token: requestUrl.token}
            , response: {
                statusName: 'code'
                , statusCode: 200
                , msgName: 'message'
                , countName: 'total'
                , dataName: 'datalist'
            }
            , cols: [[
                {checkbox: true, fixed: true}
                , {field: 'id', title: 'ID', sort: true, fixed: true}
                , {field: 'nickname', title: '姓名'}
                , {field: 'email', title: 'Email', }
                , {field: 'username', title: '手机',}
                , {field: '', title: '状态', templet:"#purchasing-agent-management-table-check",unresize: true}
                , {field: '', title: '操作', toolbar: '#purchasing-agent-management-table-bar'}
            ]]
            , page: true
            , height: 'full',

        });

        form.render(null,"purchasing-agent-management-form");
        form.on('checkbox(model)', function (data) {
            let checkedlen = $(data.elem).parents(".add-selected-option").find(".layui-form-checked").length;
            if (checkedlen < 1) {
                $(data.othis).click();
                layer.msg('至少选择一个', {icon: 6});
            } else {
                creatTable();

            }
        });
        form.on('submit(purchasing-agent-management-form-submit)',function (data) {
            requestData.name = data.field.name;
            requestData.phone = data.field.phone;
            requestData.act=act.bcCustomer_getList;
            if( data.field.status1&&data.field.status2){
                requestData.status=-1;
            }else {
                requestData.status = data.field.status1||data.field.status2;
            }
            tb.reload( {
                where: {
                    data: JSON.stringify(requestData), token: requestUrl.token,
                }
            })
            return false;
        })

        table.on("tool(purchasing-agent-management-table)",function (obj) {
            requestData={};
            requestData.act=act.bcCustomer_getInfo;
            requestData.id= obj.data.id;
            let elem = this;
            echo().data(requestData).succ(function (d) {
             if(d){
                 router({
                     pid:d,
                     elem:elem,
                     table:tb,
             })
             }
            }).loading("加载中",2).post();

        })
        form.on("switch(purchasing-agent-management-table-check)",function (obj) {
            requestData={};
            requestData.act=act.bcCustomer_updateStatus;
            requestData.id=this.value;
            requestData.status=obj.elem.checked?1:0;
            echo().data(requestData).post();
        })
    })
}

export default purchasing_agent_management;