import {imgADD, uploadConfig, uuid,act} from "../golbal/static";
import {echo} from "../golbal/commons";

Vue.component('sku-table-bk',{
    props:['skuData','modelData','model'],
    template:`<div>  <div class="layui-form-pane tag-form">
                                <div class="layui-form-item">
                                    <label class="layui-form-label">选择模板</label>
                                    <div class="layui-input-inline">
                                        <select class="template-select"  lay-search>
                                            <option v-for="(d,i) in modelData" :value="i" v-text="d.name"  v-if="i==0" selected></option>
                                            <option  v-text="d.name" :value="i" v-else></option>
                                        </select>
                                    </div>
                                     <div class="layui-inline">
                                     <button class="layui-btn layui-btn-normal add-template" @click="addModel()">添加</button>
                                    <button class="layui-btn layui-btn-normal add-template" @click="fullAll()">批量填充数据</button>
                                        </div>
                                    <div class="layui-input-inline"> <input  class="layui-input" name="model" :value="model.name" placeholder="sku分类名"></div>
                                   
                                </div>
                                </div>
                             
                           
                            
 <table class="layui-table layui-form sku-table" lay-filter="sku-template-form-bk" > <tr v-for="(data,i) in skuData">
                                            <td class="sku-td">
                                            <input name="id" :value="data.id" hidden>
                                                <div class="layui-form-item layui-inline"><label class="layui-form-label">型号</label><div class="layui-input-inline"><input class="layui-input" v-model="data.attr" name="attr" lay-verify="must"  must="请填写型号名称"></div></div>
                                                <div class="layui-form-item layui-inline"><label class="layui-form-label">销售价</label><div class="layui-input-inline"><input class="layui-input" v-model="data.price" name="price" lay-verify="price"></div></div>
                                                <div class="layui-form-item layui-inline"><label class="layui-form-label">成本价</label><div class="layui-input-inline"><input class="layui-input" v-model="data.costPrice" name="costPrice" lay-verify="notmustprice"></div></div>
                                                <div class="layui-form-item layui-inline"><label class="layui-form-label">免税价</label><div class="layui-input-inline"><input class="layui-input" v-model="data.dutyFreePrice" name="dutyFreePrice" lay-verify="notmustprice"></div></div>
                                                <div class="layui-form-item layui-inline"><label class="layui-form-label">库存</label><div class="layui-input-inline"><input class="layui-input"  v-model="data.stock" name="stock" lay-verify="notmustint"></div></div>
                                                <div class="layui-form-item layui-inline"><label class="layui-form-label">ref编码</label><div class="layui-input-inline"><input class="layui-input"  v-model="data.ref" name="ref"></div></div>
                                                <div class="layui-form-item layui-inline"><label class="layui-form-label">最少购买</label><div class="layui-input-inline"><input class="layui-input"  v-model="data.minPurchaseNum" name="minPurchaseNum"  lay-verify="int"></div></div>
                                                <div class="layui-form-item layui-inline"><label class="layui-form-label">限制条件</label><div class="layui-input-inline" @click="limit($event)"><input class="layui-input" readonly name="limitTimelimitNum"  v-model="data.limitTimelimitNum"><input hidden   name="limitTime"  v-model="data.limitTime"><input hidden name="limitNum"  v-model="data.limitNum"></div></div>
                                              <div class="layui-form-item layui-inline"><label class="layui-form-label">图片</label><div class="layui-inline"><button class=" layui-btn" @click="addImg($event.target)">上传图片</button><input name="sku-img" v-model='data.img' hidden><input type="file" accept="image/gif,image/jpeg,image/jpg,image/png" class="sku-img" @change="change($event.target,i,'skuImg')" hidden><div class="backimg-c layui-inline" style="width: 40px;height: 40px" :style='"background-image:url("+data.img+")"'>{{data.percent?data.percent:''}}</div></div></div>
                                              <div class="layui-form-item layui-inline"><label class="layui-form-label">详情图</label><div class="layui-inline"><button class=" layui-btn" @click="addImg($event.target)">上传图片</button><input name="sku-detail-img" v-model='data.detailImg' hidden><input type="file" accept="image/gif,image/jpeg,image/jpg,image/png" class="sku-img" @change="change($event.target,i,'detailImg')" hidden><div class="backimg-c layui-inline" style="width: 40px;height: 40px" :style='"background-image:url("+data.detailImg+")"'>{{data.DetialPercent?data.DetialPercent:''}}</div></div></div>
                                                <div class="layui-form-item layui-inline"><label class="layui-form-label">启用</label>
                                                <div class="layui-input-inline" v-if="data.status==1">  <input type="checkbox" name="status" lay-skin="switch" lay-text="启用|禁用" checked value="1"></div>
                                                <div class="layui-input-inline" v-else>  <input type="checkbox" name="status" lay-skin="switch" lay-text="启用|禁用" value="1" ></div>
                                                </div>
                                                <div class="layui-form-item layui-inline"><div class="layui-input-inline"><button class="layui-btn layui-btn-danger " @click="del(i)"><i class="layui-icon" >&#xe640;</i>  </button></div></div>
                                            </td>
                                        </tr>
                                           <tr v-if="skuData.length>0">
                                            <td>
                                                <button class="layui-btn layui-btn-normal" @click="add()">点击这里增加新的一行</button>
                                            </td>
                                        </tr>
                                           <tr >
                                            <td  v-if="skuData.length>0">
                                                <button class="layui-btn" lay-submit lay-filter="sku-template-form-submit-bk" type="reset" style="border: none">提交保存</button>
                                            </td>
                                        </tr>
                                        </table>
                                        </div> 
`,
    // data:function () {
    //     return{
    //         model:{
    //             name:""
    //         }
    //     }
    // }
    // ,
    methods:{
        change(elem,i,type){
            if(this.skuData[i].subscription)
                this.skuData[i].subscription.unsubscribe()
            let  putExtra = {
                fname: elem.files[0].name,
                params: {},
                // mimeType:['image/gif','image/jpeg','image/jpg','image/png'],
            };
            let key=uuid();
            let _this=this
            let observer = {
                next(res){
                    if(type=='skuImg') {
                        _this.skuData[i].percent=Math.round(res.total.percent)
                    }else {
                        _this.skuData[i].DetialPercent=Math.round(res.total.percent)
                    }
                },
                error(err){
                    $.tips("上传过程出现错误，稍后再试",4)
                    if(type=='skuImg') {
                        _this.skuData[i].percent=0
                    }else {
                        _this.skuData[i].DetialPercent=0
                    }
                },
                complete(res){
                    if(type=='skuImg') {
                        _this.skuData[i].percent=0
                    }else {
                        _this.skuData[i].DetialPercent=0
                    }
                    _this.skuData[i].uploading=false
                    if(type=='skuImg'){
                        _this.skuData[i].img=`${imgADD.add}${key}${imgADD.productImg}`
                        $(elem).next().css("background-image",`url('${ _this.skuData[i].img}')`)
                    }else {
                        _this.skuData[i].detailImg=`${imgADD.add}${key}${imgADD.productImg}`
                        $(elem).next().css("background-image",`url('${ _this.skuData[i].detailImg}')`)
                    }
                }

            }

            let observable = qiniu.upload(elem.files[0], key, this.uploadToken, putExtra, uploadConfig)
            this.skuData[i].subscription = observable.subscribe(observer)
            this.skuData[i].uploading=true
            if(type=='skuImg') {
                this.skuData[i].img = 'true'
            }else {
                this.skuData[i].detailImg = 'true'
            }
        },
        addImg(elem){
            if (!this.uploadToken) {
                echo().data({act: act.upload_upload}).succ(d => {
                    if (d) {
                        this.uploadToken = d.uploadToken;
                    } else {
                        $.tips('上传接口打开失败，请稍后再试', 4)
                    }
                }).err(() => {
                    $.tips('上传接口打开失败，请稍后再试', 4)
                }).async()

            }
            return $(elem).next().next().click();
        },
        addModel:function () {
            let v = $('.cloned .template-select').val()
            this.model.name=this.modelData[v].name
            for(let i in this.modelData[v].list){
                this.add(this.modelData[v].list[i].name)
            }
        },
        add:function (model) {
            this.skuData.push({
                attr:model,
                status:1
            })
        },
        del:function (i) {
            let skuData= this.skuData;
            layer.confirm('删除后，数据不会保留！', {icon: 3, title:'提示'}, function(index){
                if(skuData[i].id){
                    echo().data({
                        act:act.reBcBespeakProductSku_updateStatus,
                        id:skuData[i].id,
                        status:2
                    }).succ(d=>{
                        if(d){
                            skuData.splice(i,1)
                            layer.close(index)
                        }
                    }).loading('删除中').post()
                }else {
                    skuData.splice(i,1)
                    layer.close(index)
                }
            });
        },
        limit:function (elem) {
            limitOpen(elem,this.skuData)
        },
        fullAll:function () {
            let temp = $(".fullAll").clone().children(".layui-form").addClass("cloned").end().html();
            let data = this.skuData;
            let _this = this;
            layer.open({
                title:"批量填充数据",
                content:temp,
                yes:function (index,l) {
                    let $parent = $(l.selector);
                    let price= $parent.find("input[name=price]").val(),
                        costPrice = $parent.find("input[name=costPrice]").val(),
                        dutyFreePrice = $parent.find("input[name=dutyFreePrice]").val(),
                        stock = $parent.find("input[name=stock]").val(),
                        minPurchaseNum = $parent.find("input[name=minPurchaseNum]").val(),
                        limitTimelimitNum = $parent.find("input[name=limitTimelimitNum]").val(),
                        limitTime = $parent.find("input[name=limitTime]").val(),
                        limitNum = $parent.find("input[name=limitNum]").val();
                    for(let i in data){
                        data[i].price=price||data[i].price;
                        data[i].costPrice=costPrice||data[i].costPrice;
                        data[i].dutyFreePrice=dutyFreePrice||data[i].dutyFreePrice;
                        data[i].stock=stock||data[i].stock;
                        data[i].limitTimelimitNum=limitTimelimitNum||data[i].limitTimelimitNum;
                        data[i].minPurchaseNum=minPurchaseNum||data[i].minPurchaseNum;
                        data[i].limitTime=limitTime||data[i].limitTime;
                        data[i].limitNum=limitNum||data[i].limitNum;
                    }
                    layer.close(index)
                }
            });
        }
    },
    updated:function () {
        layui.use('form',function () {
            layui.form.render(null,'sku-template-form-bk')
        })


    }
});
function limitOpen(elem,data) {
    layer.open({
        type:1,
        title:'限购条件',
        content:'',
        btn: ['每周', '每月', '每季','每年'],
        btn4:function (index,layero) {
            limitNum({
                elem:elem.target||elem,
                title:this.btn[3],
                limit1:4,
                data:data
            })
            layer.close(index)
        },
        btn3:function (index,layero) {
            limitNum({
                elem:elem.target||elem,
                title:this.btn[2],
                limit1:3,
                data:data
            })
            layer.close(index)
        },
        btn2:function (index,layero) {
            limitNum({
                elem:elem.target||elem,
                title:this.btn[1],
                limit1:2,
                data:data
            })
            layer.close(index)
        },
        yes:function (index,layero) {
            limitNum({
                elem:elem.target||elem,
                title:this.btn[0],
                limit1:1,
                data:data
            })
            layer.close(index)
        },


    })
}
function limitNum(d) {
    layer.prompt({
        title: '请输入值',
    },function(value, index, elem){

        $(d.elem).val(`${d.title}限购${value}件`).next().val(d.limit1).next().val(value);
        d.elem.dispatchEvent(new Event('input'))
        $(d.elem).next()[0].dispatchEvent(new Event('input'))
        $(d.elem).next().next()[0].dispatchEvent(new Event('input'))

        layer.close(index)
    });
}