// 分类编辑
import {echo} from "../golbal/commons";
import {act} from '../golbal/static.js'
import {imgADD, putExtra, uploadConfig, uuid} from "../golbal/static";


let classification_of_goods_edit= function (d) {
    let isuploading=false,key='';
    let id = d.id,pid=d.pid,tb=d.table;
    let data={
        options:[],
        clazz:{}
    };

    if(pid){
        data.clazz=pid;
    }
    layui.use(['form','upload'],function () {
    let vue =new Vue({
        el:`.c${id}`,
        data:data,
        delimiters: ['${{', '}}'],
        updated:function(){
            form.render(null,"classification-of-goods-edit-form");
        },
    });
        let form = layui.form,
            upload = layui.upload,
            requestdata={};
        requestdata.act=act.bcCategory_getParentList;
        echo().data(requestdata).succ(function (d) {
            vue.options=d
        }).post();

       let goods_main_img = upload.render({
            elem: `.c${id} #classification-of-goods-edit-upload`
           ,auto: false
            , choose: function (obj) {
                echo().data({
                    act:act.upload_upload
                }).succ(d=>{
                    if(d){
                        let f =  $(`.c${id} #classification-of-goods-edit-upload-img`).parents(".layui-upload").find("input")[0].files[0]
                        let observer = {
                            next(res){
                            },
                            error(err){
                                $.tips("上传过程出现错误，稍后再试",4)
                            },
                            complete(res){
                                isuploading=false
                                layer.closeAll()
                                $(`.c${id} #classification-of-goods-edit-upload-img`).attr('src', `${imgADD.add}${key}${imgADD.brandImg}`);
                            }
                        }
                         key = uuid()
                        let observable = qiniu.upload(f,key , d.uploadToken, putExtra, uploadConfig)
                       observable.subscribe(observer)
                        isuploading=true
                       $.tips(1)
                        //预读本地文件示例，不支持ie8
                        // obj.preview(function (index, file, result) {
                        //     $(`.c${id} #classification-of-goods-edit-upload-img`).attr('src', result);
                        // });
                    }else {
                        isuploading=false
                    }
                }).err(()=> isuploading=false).post()

            }
        });

        form.on('submit(classification-of-goods-edit)',function (data) {
            if(isuploading)
                $.tips('正在上传图片中，请等图片上传结束后再提交',3)
            let tips,
                requestdata = data.field;
                let img = $(data.form).find(`#classification-of-goods-edit-upload-img`).attr('src');
                if(img){
                    requestdata.img=img;
                }
                let f = $(data.form).attr("flag")=='edit'
                if(f){
                    requestdata.act=act.bcCategory_update;
                    tips="修改";
                }else {
                    requestdata.act=act.bcCategory_add;
                    tips="添加";
                }
                   let isSucc = false;
                echo().data(requestdata).succ(function (d) {
                    if(d){
                        isSucc = true;
                        $.tips(`${tips}成功`,3);
                        if(!f) {
                            key = '';
                            $(data.form).find(`#classification-of-goods-edit-upload-img`).attr('src','');
                        }
                        tb.reload();
                    }
                }).loading("上传中",2).async();
                if(f)
                    return false;
                return isSucc;
        })
    })
};
export default classification_of_goods_edit;

