let wuliuCom;
let openYunDan=d=>{
    if(!wuliuCom){
        echo().data({
            act:act.company_getList
        }).loading().succ(d=>{
            if(d){
                wuliuCom=d
            }
        }).async()
    }

    layer.open({
        type: 1,
        area:['600px','600px'],
        content:`<div class="layui-form" style="margin-top: 20px"  lay-filter="yundan-form" id="yundan-form">
               <div class="layui-form-item">
              <label  class="layui-form-label label-w120">运输公司</label>
               <div class="layui-input-block" >
                  ${(companies => {
            let s = '';
            for (let i in companies) {
                s +=`<input type="radio" name="wuliu" value=${companies[i].code} title="${companies[i].name}" class="yundan-com">`
            }
            return s
        })(wuliuCom)}
    

      
        </div>
        </div>
        <div class="layui-form-item" >
              <label  class="layui-form-label label-w120">运单号</label>
               <div class="layui-input-inline" style="width: 250px">
                <input  type="text" id="yundan-no" placeholder="请输入运单号"  class="layui-input" >
        </div>
    </div>
    <div class="padding15">
    <ul class="layui-timeline yundan-info">
    
   ${(orderSn=>{
       if(orderSn)
           return ` <li class="layui-timeline-item">
    <i class="layui-icon layui-timeline-axis">&#xe63f;</i>
    <div class="layui-timeline-content layui-text">
      <p>
      获取物流信息中
      </p>
    </div>
  </li>`
            return ''
        })(d.splitOrderSn||d.orderSn)}
 

</ul>
</div>
</div>`
        ,btn: ['确定', '取消'],
        closeBtn:0,
        title:'运单信息'
        ,yes: function(index, layero){
            //按钮【按钮一】的回调
            let code = $("#yundan-form .layui-form-radioed").prev().val();
            if(!code){
                $.tips('请选择物流公司',4)
                return false
            }
            let no =  $("#yundan-no").val();
            if(!no){
                $.tips('请输入运单号',4)
                return false
            }
          d.succ(code,no)
            layer.close(index)
        }
        ,btn2: function(index, layero){
            layer.close(index)
        }
    });
    if(d.splitOrderSn||d.orderSn){
        echo().data({
            act:d.yundanInfoAct,
            splitOrderSn:d.splitOrderSn,
            orderSn:d.orderSn
        }).succ(d=>{
            let str= ''
                for(let i in d.list){
                    str += ` <li class="layui-timeline-item">
    <i class="layui-icon layui-timeline-axis">&#xe63f;</i>
    <div class="layui-timeline-content layui-text">
      <p>
      ${d.list[i].time}:${d.list[i].context} 
      </p>
    </div>
  </li>`
                }
                $(".yundan-info").html(str)
        }).post();
    }
if(d.form)
    d.form.render(null,'yundan-form')
}
