let openRefund=(d)=>{

    layer.open({
        type: 1,
        area:["400px"],
        content:`<div class="layui-form" style="margin-top: 20px"  lay-filter="refund-form" id="refund-form">
        <div class="layui-form-item" >
              <label  class="layui-form-label">退款数量</label>
               <div class="layui-input-inline" style="width: 250px">
                <input  type="text" id="refund-num" placeholder="退款数量"  class="layui-input" >
        </div>
        </div>
          <div class="layui-form-item" >
              <label  class="layui-form-label">退款原因</label>
               <div class="layui-input-inline" style="width: 250px">
                <input  type="text" id="refund-result" placeholder="退款原因"  class="layui-input" >
        </div>
    </div>
</div>`
        ,btn: ['确定', '取消'],
        closeBtn:0,
        title:'退款'
        ,yes: function(index, layero){
            //按钮【按钮一】的回调
            let refund_num=$("#refund-num").val();
            if(!/^[0-9]+$/.test(refund_num)){
                $.tips('退款数量必须是整数',4)
                return false
            }else if(Number.parseInt(refund_num)<1){
                $.tips('退款数量必须大于0',4)
                return false
            }
            if(refund_num>d.num){
                $.tips('退款数量大于实际数量',4)
                return false
            }
            let refund_result =  $("#refund-num").val();
            if(!refund_result){
                $.tips('请输入退款原因',4)
                return false
            }
            d.succ(refund_num,refund_result)
            layer.close(index)
        }
        ,btn2: function(index, layero){
            layer.close(index)
        }
    });
    if(d.form)
        d.form.render(null,'yundan-form')
}
