
import {requestUrl,act} from '../golbal/static.js'
import router from "../plugins/router";
import {echo} from "../golbal/commons";
//型号管理
let t,table,requestData={};
let classification_of_goods_classification =function (d) {
    let id = d.id;
    new Vue({
        el:`.c${id}`,
        delimiters: ['${{', '}}'],
    });

    layui.use(['table', 'form'], function () {
      table = layui.table;
        let form = layui.form;
        requestData.act = act.bcTemplate_getList;
        t=table.render({
           elem: `.c${id} #classification-of-classification-goods`
           , url: requestUrl.mainUrl
           , method: "post"
           , where: {data: JSON.stringify(requestData), token: requestUrl.token}
           , response: {
             statusName: 'code'
                , statusCode: 200
                , msgName: 'message'
                , countName: 'total'
                , dataName: 'datalist'
          },
           cols: [[ //表头
            {field: 'id', title: 'ID',  sort: true, fixed: 'left'}
            ,{field: 'name', title: '模板名称',}
            ,{field: 'list', title: '子名称',templet: "#classification-of-goods-classification-son",unresize: true}
            ,{field: 'status', title: '状态',templet: "#classification-of-goods-classification-check", unresize: true}
            ,{field: '', title: '操作', toolbar:"#classification-of-goods-classification-bar"}
        ]]
           ,page: true,
           id:"classification_of_goods_classification"
       })
        table.on('tool(classification-of-classification-goods)',function (obj) {
            router({
                pid: obj.data,
                elem:this,
                table:t,
            })
        });
        form.on('switch(classification-of-goods-classification-check)',function(obj){
            let requestData={};
            requestData.act=act.bcTemplate_updateStatus;
            requestData.id=this.value;
            requestData.status=obj.elem.checked?1:0;
            echo().data(requestData).post();
        })
    })
}


export default classification_of_goods_classification;
$(function () {
    let $body=$("body");
    $body.on('click','.cloned  .new-model',function () {
        router({
            elem:this,
            table:t,
        })
    })

});