import {act, openModifyAdd, openTable, openYunDan, requestUrl} from "../golbal/static";
import {echo} from "../golbal/commons";
import router from "../plugins/router";

let  table,requestData,form,t;
let commodity_return =function (d) {
    layui.use(['upload', 'form', 'element', 'table','laydate'], function () {
        form = layui.form
        let id = d.id;
        let vu = new Vue({
            el:`.c${id}`,
            delimiters: ['${{', '}}'],
        });
        requestData={
            act:act.bcRefundOrder_getList
        }
        table = layui.table;
        t=table.render({
            elem: `.c${id} #commodity-return-list-table`
            , url: requestUrl.mainUrl
            , method: "post"
            , where: {data: JSON.stringify(requestData), token: requestUrl.token}
            , response: {
                statusName: 'code'
                , statusCode: 200
                , msgName: 'message'
                , countName: 'total'
                , dataName: 'datalist'
            }
            ,cols: [[
                {field:'refundSn', title: '退款编号'}
                ,{field:'orderSn', title: '订单编号'}
                ,{field:'', title: '商品清单',toolbar:"#commodity-return-list-table-items"}
                ,{field:'nickname', title: '客户'}
                ,{field:'orderStatus', title: '状态'}
                ,{field:'price', title: '退款总价（元）'}
                ,{field:'', title: '操作',  toolbar: '#commodity-return-list-table-bar1'}
            ]]
            ,page: true,
        });
        form.render(null,'commodity-return-list-clone-form')
        form.on('submit(commodity-return-list-clone-form-search)',function (obj) {
            requestData=obj.field;
            requestData.act=act.bcRefundOrder_getList
            t.reload( {
                where: {
                    data: JSON.stringify(requestData), token: requestUrl.token,
                }
                ,page: {
                    curr: 1
                }
            })
        })
        table.on("tool(commodity-return-list-table)",function (obj) {
            let layEvent = obj.event,data = obj.data;
            switch (layEvent){
                case "order-detail":
                    openTable(data.items)
                    break;
                case 'edit':
                    requestData={
                        act:act.bcRefundOrder_getInfo,
                        refundSn:data.refundSn
                    };
                    echo().data(requestData).succ(d=>{
                        if(d) {
                            router({
                                pid: d,
                                elem: this,
                                table: t,
                            })
                        }
                    }).loading().post()
                break}
        })
})
}
export default commodity_return