import {act, requestUrl} from "../golbal/static";
import {echo} from "../golbal/commons";
import router from "../plugins/router";

let requestData,tb;
let special_offer=function (d) {
    let id = d.id;

    new Vue({
        el: `.c${id}`,
        delimiters: ['${{', '}}'],
        mounted:function () {
            layui.use(['table' ,'form'], function () {
            let table = layui.table,form=layui.form;
            requestData={};
            requestData.act=act.reBcSpecialProduct_getList;

             tb = table.render({
                elem: `#special-offer-table`
                , url: requestUrl.mainUrl
                , method: "post"
                , where: {data: JSON.stringify(requestData), token: requestUrl.token}
                , response: {
                    statusName: 'code'
                    , statusCode: 200
                    , msgName: 'message'
                    , countName: 'total'
                    , dataName: 'datalist'
                }
                    ,cols: [[
                        {field:'id', title: '编号'}
                        ,{field:'name', title: '商品'}
                        ,{field:'attr', title: '商品型号'}
                        ,{field:'price', title: '特价'}
                        ,{field:'originalPrice', title: '原价'}
                        ,{field:'priority', title: '排序'}
                        ,{field:'status', title: '状态'}
                        ,{field: '', title: '操作', toolbar:"#special-offer-table-bar"}

                    ]]
                    ,page: true,

                }
            )

                form.on("submit(special-offer-search-table)",function (data) {
                    requestData=data.field;
                    requestData.act=act.reBcSpecialProduct_getList;
                    echo().data(requestData).succ(function (d) {
                        if(d){
                            tb.reload({
                                where: {
                                    data: JSON.stringify(requestData), token: requestUrl.token,
                                }
                                ,page: {
                                    curr: 1 //重新从第 1 页开始
                                }
                            })
                        }
                    }).loading("加载中",2).post();

                })
                //监听编辑
                table.on("tool(special-offer-table)",function (obj) {
                    requestData={};
                    requestData.act=act.reBcSpecialProduct_getInfo;
                    requestData.id = obj.data.id;
                    let elem=this;
                    echo().data(requestData).succ(function (d) {
                        router({
                            pid: d,
                            elem:elem,
                            table:tb,
                        })
                    }).loading("加载中",2).post();
                })
        })

            //mounted
        }
        //vue
    });

}

export default special_offer;

let $body=$("body");
$body.on('click','.cloned .add-special-offer-btn',function () {
    router({
        elem:this,
        table:tb,
    })
})