import router from "../plugins/router";
import {act, openTable, openYunDan, requestUrl} from "../golbal/static";
import {echo} from "../golbal/commons";
let delivery_management =function (d) {
    let id = d.id;
    let vu = new Vue({
        el:`.c${id}`,
        delimiters: ['${{', '}}'],
    });
let t,requestData;
    layui.use(['table' ,'form','laydate'], function () {
        let laydate = layui.laydate,form = layui.form,  table = layui.table;
        form.render(null,'delivery-management-form')
        requestData={
            act:act.bcSplitOrder_getList
        }
        t=table.render({
            elem: `.c${id} #delivery-management-table`
            , url: requestUrl.mainUrl
            , method: "post"
            , where: {data: JSON.stringify(requestData), token: requestUrl.token}
            , response: {
                statusName: 'code'
                , statusCode: 200
                , msgName: 'message'
                , countName: 'total'
                , dataName: 'datalist'
            }
            ,cols: [[
                {field:'orderSn', title: '主订单编号'}
                ,{field:'splitOrderSn', title: '子订单编号'}
                ,{field:'', title: '商品清单',toolbar:"#stock-order-list-table-items"}
                ,{field:'dispatchRegion', title: '发货地'}
                ,{field:'phone', title: '客户手机号'}
                ,{field:'orderStatus', title: '状态'}
                ,{field:'price', title: '子订单总价（元）'}
                ,{field:'', title: '操作',  toolbar: '#delivery-management-table-bar'}
            ]]
            ,page: true,
        });

        table.on("tool(delivery-management-table)",function (obj) {
            let layEvent = obj.event,data = obj.data;
            switch (layEvent){
                case "order-detail":
                    openTable(data.items)
                    break;
                case "waybill-number":
                    openYunDan({
                        form:form,
                        splitOrderSn:data.splitOrderSn,
                        yundanInfoAct:act.bcSplitOrder_getDelivery,
                        succ:function (code,no) {
                            echo().data({
                                act:act.bcSplitOrder_delivery,
                                splitOrderSn:data.splitOrderSn,
                                code:code,
                                deliverySn:no,
                            }).succ(d=>{
                                if(d)
                                    t.reload()
                            }).loading().post()
                        }
                    });
                    break;

                case 'edit':
                    requestData={
                        act:act.bcSplitOrder_getInfo,
                        splitOrderSn:data.splitOrderSn
                    };
                    echo().data(requestData).succ(d=>{
                        if(d) {
                            router({
                                pid: d,
                                elem: this,
                                table: t,
                            })
                        }
                    }).loading().post()
                    break;

            }

        })

        form.on('submit(delivery-management-form-submit)',function (obj) {
            requestData=obj.field;
            requestData.act=act.bcSplitOrder_getList
                t.reload( {
                    where: {
                        data: JSON.stringify(requestData), token: requestUrl.token,
                    }
                    ,page: {
                        curr: 1
                    }
                })
        })
    })
}

export default  delivery_management;