import {act} from "../golbal/static";
import {echo} from "../golbal/commons";

Vue.component('special-offer-edit',{
    props:{
        specialOffer:Object,
        flag:String
       },
    data:function () {
      return {
          isShow:false,
          isAttrShow:false,
      }
    },
    template: `<div>
   <h2-title title="特价配置"></h2-title>
   <div class="layui-form" lay-filter="special-offer-edit" :flag="flag">
     <div class="layui-form-item">
        <label class="layui-form-label">商品类型</label>
        <div class="layui-input-inline">
        <select disabled>
        <option value="1">一件代发</option>
        <option value="2">预约爆款</option>
        </select>
        </div>
    </div>
     <div class="layui-form-item">
        <label class="layui-form-label">商品</label>
        <div class="layui-input-inline">
        <div v-if="specialOffer.id">
           <input name="id" hidden :value="specialOffer.id">
           <input class="layui-input layui-disabled" :value="specialOffer.name" readonly>
           </div>
        <div class="layui-form-select layui-form-selected" v-else>
        <div class="layui-select-title">
        <input type="text" placeholder="请输入商品名" v-model="specialOffer.goodsName" id="query-goods" class="layui-input" @keydown="queryGoods($event)" lay-verify="must" must="请添加商品">
        <input type="text"  name="pid" v-model="specialOffer.pid" hidden>
        <i class="layui-edge"></i></div>
        <dl class="layui-anim layui-anim-upbit query-goods" style="">
        <dd  v-for="good in specialOffer.goods" v-text="good.name" @click="goodsSelect(good)"></dd>
        </dl></div>
       
       
        </div>
    </div>
     <div class="layui-form-item">
        <label class="layui-form-label">商品型号</label>
        <div class="layui-input-inline" v-if="specialOffer.attr">
          <input class="layui-input layui-disabled" name="skuId" :value="specialOffer.attr" readonly>
        </div>
        <div class="layui-input-inline" v-else>
            <select  lay-filter="specialOffer-attr-select" id="specialOffer-select" name="skuId" lay-verify="selectthan0" info="一个型号"> 
            <option value="0" selected disabled>请选择</option>
            <option v-for="attr in specialOffer.attrs" v-text="attr.attr" :value="attr.id" :price="attr.price" ></option>
            </select>
        </div>
      
    </div>
     <div class="layui-form-item">
        <label class="layui-form-label">特价</label>
        <div class="layui-input-inline"><input class="layui-input" name="price" :value="specialOffer.price" lay-verify="price"></div>
    </div>
     <div class="layui-form-item">
        <label class="layui-form-label">原价</label>
        <div class="layui-input-inline"><input class="layui-input layui-disabled" name="originalPrice" id="specialOffer-price" :value="specialOffer.originalPrice" readonly></div>
    </div>
     <div class="layui-form-item">
        <label class="layui-form-label">排序</label>
        <div class="layui-input-inline"><input class="layui-input" name="priority" :value="specialOffer.priority" lay-verify="int"></div>
    </div>
     <div class="layui-form-item">
        <label class="layui-form-label">生效时间</label>
        <div class="layui-input-block"><input :value="specialOffer.specialOfferTime" class="layui-input" id="special-offer-time" lay-verify="must" must="请选择生效日期"  readonly>
        <input  id="special-offer-start" name="startTime" :value="specialOffer.startTime" hidden>
        <input  id="special-offer-end" name="endTime" :value="specialOffer.endTime" hidden>
        </div>
    </div>
 
     <div class="layui-form-item">
        <label class="layui-form-label"></label>
        <div class="layui-input-inline"><button class="layui-btn" lay-submit lay-filter="special-offer-edit-submit">提交</button></div>
    </div>

</div>
</div>
    `,
    mounted:function () {
        layui.use(['form','laydate'], function () {
          layui.form.render(null,'special-offer-edit')
            layui.laydate.render({
                elem: '#special-offer-time',
                type:'datetime',
                range: '至',
                done:function (value,date,endDate) {
                    if(!$.stringIsBlank(value)) {
                        let v = value.split("至")
                        $("#special-offer-start").val(v[0].trim())
                        $("#special-offer-end").val(v[1].trim())
                    }

                }
            });
            layui.laydate.render({
                elem: '#special-offer-end',
                type:'datetime'
            });
        })
    },
    updated:function () {
        if(this.isShow) {
            $(".query-goods").addClass("layui-show");
        }
        if(this.isAttrShow){
            layui.form.render("select","special-offer-edit");
            this.isAttrShow=false;
        }

    }
    ,methods:{
        queryGoods:function (event) {
            if(event.keyCode==13){
                let name = $("#query-goods").val();
              let requestData={};
              requestData.name=name;
              requestData.act=act.reBcSpecialProduct_getProductList;
              let vuethis=this;
              echo().data(requestData).succ(function (d) {
                  vuethis.isShow=true;
                  vuethis.specialOffer.goods=d;
              }).loading().post();
            }
        },
        goodsSelect:function (good) {
            this.specialOffer.goodsName=good.name;
            this.isShow=false;
            this.specialOffer.pid=good.id;
            $(".query-goods").removeClass("layui-show");
            let requestData={},vuethis=this;
            requestData.act=act.reBcSpecialProduct_getSkuList;
            requestData.id=good.id;
            echo().data(requestData).succ(function (d) {
                if(d){
                    vuethis.specialOffer.attrs=d;
                    vuethis.isAttrShow=true;
                }
            }).loading().post();

        }
    }
    //vue
})