import {act, openModifyAdd, openYunDan} from "../golbal/static";
import {echo} from "../golbal/commons";

let  table,requestData,form,t;
let commodity_return_edit =function (d) {
    layui.use(['upload', 'form', 'element', 'table','laydate'], function () {
        form = layui.form
        let id = d.id;
            t=d.table
        let vu = new Vue({
            el:`.c${id}`,
            delimiters: ['${{', '}}'],
            data:d.pid,

        });

        table = layui.table;
        table.render({
            elem: `.c${id} #commodity-return-edit-table`
            ,url: ''
            ,cols: [[
                {field:'brandName', title: '品牌'},
                {field:'productName', title: '商品名称',width:'30%'}
                ,{field:'attr', title: '型号'}
                ,{field:'ref', title: 'REF'}
                ,{field:'num', title: '数量'}
                ,{field:'productPrice', title: '单价（元）'}
                ,{field:'total', title: '总价（元）'}
                ,
            ]]
            ,page: true,
            data: d.pid.items
        });
        form.on('submit(commodity-return-dismissal)',function (obj) {
            layer.prompt({
                value: '',
                title: '驳回原因',
            }, function (v, i,e) {
                    echo().data({
                        act:act.bcRefundOrder_reject,
                        refundSn:vu.refundSn,
                        reason:v
                    }).loading().succ(d=>{
                        if(d){
                            $.tips('驳回成功',3)
                            echo().data({
                                act:act.bcRefundOrder_getInfo,
                                refundSn:vu.refundSn
                            }).succ(dd=>{
                                vu.orderStatus=dd.orderStatus
                            }).loading().post()
                        }
                    }).post()
                layer.close(i)
            })
        })
        form.on('submit(commodity-return-refund)',function (obj) {
            let price=obj.field.price;
            if(!(/^[0-9]+\.?[0-9]*$/.test(price))){
                $.tips("价格不合法",4)
                return false
            }
            layer.confirm('确定退款?', function(index){
                echo().data({
                    act:act.bcRefundOrder_validate,
                    refundSn:vu.refundSn,
                    price:price
                }).loading().succ(d=>{
                    if(d){
                        $.tips('退款成功',3)
                        t.reload()
                        echo().data({
                            act:act.bcRefundOrder_getInfo,
                            refundSn:vu.refundSn
                        }).succ(dd=>{
                            vu.orderStatus=dd.orderStatus
                            vu.hasRefund=dd.hasRefund
                        }).loading().post()
                    }
                }).post()

                layer.close(index);
            });

        })

    })
}

export default commodity_return_edit;