import {echo} from '../golbal/commons.js'
import {requestUrl,act} from '../golbal/static.js'
// 商品分类
let table,requestData={},t;
import router from "../plugins/router";
let classification_of_good_index_bk = function (d) {
    let id = d.id;
    new Vue({
        el: `.c${id}`,
        delimiters: ['${{', '}}'],
    })

    layui.use(['table', 'form'], function () {
        table = layui.table;
        let form = layui.form;
        requestData.act = act.bcBespeakCategory_getList;
        t= table.render({
            elem: `.c${id} #classification-of-goods-bk`
            , url: requestUrl.mainUrl
            , method: "post"
            , where: {data: JSON.stringify(requestData), token: requestUrl.token}
            , response: {
                statusName: 'code'
                , statusCode: 200
                , msgName: 'message'
                , countName: 'total'
                , dataName: 'datalist'
            }
            , cols: [[
                {checkbox: true, fixed: true}
                , {field: 'id', title: 'id', sort: true, fixed: true,}
                , {field: 'name', title: '名称', sort: true}
                , {field: 'status', title: '状态', sort: true, templet: "#classification-of-goods-bk-check", unresize: true}
                , {field: '', title: '操作', toolbar: '#classification-of-goods-bk-bar'}

            ]]
            , page: true
            , loading: true
        });

        form.render();
        //监控提交事件
        form.on('submit(classification-bk-table)', function (data) {
            requestData={};
            requestData.name = data.field.name;
            requestData.act= act.bcBespeakCategory_getList;
            if( data.field.status1&&data.field.status2){
                requestData.status=-1;
            }else {
                requestData.status = data.field.status1||data.field.status2;
            }
            t.reload( {
                where: {
                    data: JSON.stringify(requestData), token: requestUrl.token,
                }
                ,page: {
                    curr: 1 //重新从第 1 页开始
                }
            })
            return false;
        });

        //监控状态开关
      form.on('switch(classification-of-goods-bk-check)',function(obj){
          let requestData={};
          requestData.act=act.bcBespeakCategory_updateStatus;
          requestData.id=this.value;
          requestData.status=obj.elem.checked?1:0;
          echo().data(requestData).post();
      })
    });
    table.on('tool(classification-of-goods-bk)',function (obj) {
        requestData={};
        requestData.act=act.bcBespeakCategory_getInfo;
        requestData.id = obj.data.id;
        let elem=this;
            echo().data(requestData).succ(function (d) {
                router({
                    pid: d,
                    elem:elem,
                    table:t,
                })
            }).loading("加载中",2).post();


    });
}
let $body = $("body");
$body.on("click",".classification-of-goods-bk-crate-btn",function () {

    router({
        elem:this,
        table:t,
    })
})
export default  classification_of_good_index_bk;