import router from "../plugins/router";
import {requestUrl,act} from "../golbal/static";
import {echo} from "../golbal/commons";

let tb,requestData;
let admin_management= function (d) {
    let id = d.id;
    new Vue({
        el:`.c${id}`,
        delimiters: ['${{', '}}'],
    })
    requestData = {}
    requestData.act=act.bcPurchase_getList;

    layui.use(['table','form'], function () {
        var table = layui.table,form=layui.form;
       tb=table.render({
            elem: `.c${id} .admin-management-table`
               , url: requestUrl.mainUrl
               , method: "post"
               , where: {data: JSON.stringify(requestData), token: requestUrl.token}
               , response: {
                   statusName: 'code'
                   , statusCode: 200
                   , msgName: 'message'
                   , countName: 'total'
                   , dataName: 'datalist'
               }
            , cols: [[
                {checkbox: true, fixed: true}
                , {field: 'id', title: 'ID', sort: true, fixed: true}
                ,{field:'nickname', title: '管理员姓名',}
                , {field: 'phone', title: '手机号码',}
                , {field: 'username', title: '账号',}
                , {field: 'type', title: '权限',templet: "#admin-management-table-type"}
                , {field: 'status', title: '状态', templet: "#admin-management-table-check",}
                , {field: '', title: '操作', toolbar: '#admin-management-table-bar'}
            ]]
            , page: true
            , height: 'full',
        });
        table.on('tool(admin-management-table)',function (obj) {
            router({
                    pid: obj.data,
                    elem:this,
                    table:tb,
                    type:'edit'
                })
        })
        form.on('switch(admin-management-table-check)',function (obj) {
            requestData={};
            requestData.act=act.bcPurchase_updateStatus;
            requestData.id=this.value;
            requestData.status=obj.elem.checked?1:0;
            echo().data(requestData).post();
        })
    });


}
export default admin_management;
let $body=$("body");
$body.on("click",".add-admin-btn",function () {
    router({
        elem:this,
        table:tb,
        type:'add'
    })
})