//现货订单管理
import {act, openTable, openYunDan, requestUrl} from "../golbal/static";
import {echo} from "../golbal/commons";
import router from "../plugins/router";

let  orderlist,table,t,requestData,searchStatus;
let bk_order_list =function (d) {
    let id = d.id;
    new Vue({
        el:`.c${id}`,
        delimiters: ['${{', '}}'],
    });

    layui.use(['upload', 'form', 'element', 'table','laydate'], function () {
        let laydate = layui.laydate,form = layui.form;
        form.render(null,'stock-order-search-form');
        laydate.render({
            elem: `.c${id} .order-start` ,type:"datetime"}
        )
        laydate.render({
            elem: `.c${id} .order-end` ,type:"datetime"
        });
        requestData={
            act:act.bcBespeakOrder_getList
        }
        table = layui.table;
        t = table.render({
            elem: `.c${id} #stock-order-list-table-bk`
            , url: requestUrl.mainUrl
            , method: "post"
            , where: {data: JSON.stringify(requestData), token: requestUrl.token}
            , response: {
                statusName: 'code'
                , statusCode: 200
                , msgName: 'message'
                , countName: 'total'
                , dataName: 'datalist'
            }
            ,cols: [[
                {checkbox: true, fixed: true},
                {field:'orderSn', title: '订单号', sort: true, fixed: true}
                ,{field:'phone', title: '客户手机'}
                ,{field:'orderStatus', title: '状态' }
                ,{field:'', title: '商品清单',toolbar:"#stock-order-list-table-items-bk"}
                ,{field:'address', title: '地址'}
                ,{field:'restTime', title: '剩余抢购时间'}
                ,{field:'', title: '操作', width:270, toolbar: '#stock-order-list-table-bar1-bk'}

            ]],
            id: `.c${id}stock-order-list-table-bk`
            ,page: true,

        });
        form.on("submit(stock-order-search-form-submit-bk)",function (obj) {
            searchStatus=obj.field.orderStatus
            requestData=obj.field;
            requestData.act=act.bcBespeakOrder_getList
            t.reload( {
                where: {
                    data: JSON.stringify(requestData), token: requestUrl.token,
                }
                ,page: {
                    curr: 1 //重新从第 1 页开始
                }
            })
        })


        form.on("submit(taking-order)",function (obj) {
            if(searchStatus=='pendingOrder'){
                let checkStatus = table.checkStatus(`.c${id}stock-order-list-table-bk` )
                    ,data = checkStatus.data;
                if(data.length<1){
                    $.tips('请至少选择一个',4)
                }else {
                    let orderSns = [];
                    for (let i in data) {
                        orderSns.push(data[i].orderSn)
                    }
                    echo().data({
                        act:act.bcBespeakOrder_takingOrder,
                        orderSns:orderSns,
                    }).succ(d=>{
                        if(d)
                            t.reload()
                    }).loading().post()
                }
            }else {
                $.tips('请筛选订单后再操作',4)
            }
        })

        form.on("submit(order-confirm)",function (obj) {
            if(searchStatus=='waitconfirm'){
                let checkStatus = table.checkStatus(`.c${id}stock-order-list-table-bk` )
                    ,data = checkStatus.data;
                if(data.length<1){
                    $.tips('请至少选择一个',4)
                }else {
                    layer.confirm('确认吗?', function(index){
                        let orderSns = [];
                        for (let i in data) {
                            orderSns.push(data[i].orderSn)
                        }
                        requestData={
                            act:act.bcBespeakOrder_confirm,
                            orderSns:orderSns
                        };
                        echo().data(requestData).succ(d=>{
                            if (d){
                                t.reload()
                            }
                        }).loading().post();
                        layer.close(index);
                    });

                }
            }else {
                $.tips('请筛选订单后再操作',4)
            }
        })
        form.on("submit(waybill-number-submit-bk)",function (obj) {
            if(searchStatus=='pendingDelivery'||searchStatus=='delivered'||searchStatus=='complete'){
                let checkStatus = table.checkStatus(`.c${id}stock-order-list-table-bk` )
                    ,data = checkStatus.data;
                if(data.length<1){
                    $.tips('请至少选择一个',4)
                }else {
                    let orderSns=[];
                    for(let i in data){
                        orderSns.push(data[i].orderSn)
                    }
                    openYunDan({
                        form:form,
                        succ:function (code,no) {
                            echo().data({
                                act:act.bcBespeakOrder_batchDelivery,
                                orderSns:orderSns,
                                code:code,
                                deliverySn:no,
                            }).succ(d=>{
                                if(d)
                                    t.reload()
                            }).loading().post()
                        }
                    });
                }

            }else {
                $.tips('请筛选订单后再操作',4)
            }

            return false
        })
        table.on("tool(stock-order-list-table-bk)",function (obj) {
            let layEvent = obj.event,data = obj.data;
            switch (layEvent){
                case "order-detail":
                    openTable(data.items)
                    break;
                case "waybill-number":
                    openYunDan({
                        form:form,
                        orderSn:data.orderSn,
                        yundanInfoAct:act.bcBespeakOrder_getDelivery,
                        succ:function (code,no) {
                            echo().data({
                                act:act.bcBespeakOrder_batchDelivery,
                                orderSns:[data.orderSn],
                                code:code,
                                deliverySn:no,
                            }).succ(d=>{
                                if(d)
                                    t.reload()
                            }).loading().post()
                        }
                    });
                    // layer.prompt({
                    //     formType:0,
                    //     title:"请输入运单号",
                    // },function (v,i,e) {
                    //     layer.close(i);
                    // })
                    break;
                case "refund":   layer.prompt({
                    formType:0,
                    title:"请输入退款原因",
                },function (v,i,e) {
                    layer.close(i);
                })
                    break;
                case "cancel":
                    layer.prompt({
                        formType:0,
                        title:"请输入取消原因",
                        value: ' ',
                    },function (v,i,e) {
                        requestData={
                            act:act.bcBespeakOrder_cancel,
                            orderSn:data.orderSn,
                            remark:v
                        }
                        echo().data(requestData).succ(d=>{
                            if (d){
                                t.reload()
                            }
                        }).loading().post();
                        layer.close(i);
                    })
                    break;
                case 'edit':
                    requestData={
                        act:act.bcBespeakOrder_getInfo,
                        orderSn:data.orderSn
                    };
                    echo().data(requestData).succ(d=>{
                        if(d) {
                            router({
                                pid: d,
                                elem: this,
                                table: t,
                            })
                        }
                    }).loading().post()
                    break;
                case 'replaceOrder':
                    layer.confirm('确定代下单?', function(index){
                        requestData={
                            act:act.bcBespeakOrder_replaceOrder,
                            orderSn:data.orderSn
                        };
                        echo().data(requestData).succ(d=>{
                            if (d){
                                t.reload()
                            }
                        }).loading().post();
                        layer.close(index);
                    });

                    break;
                case 'takingOrder':

                        requestData={
                            act:act.bcBespeakOrder_takingOrder,
                            orderSns:[data.orderSn]
                        };
                        echo().data(requestData).succ(d=>{
                            if (d){
                                t.reload()
                            }
                        }).loading().post();

                    break;
                case "confirm":
                    layer.confirm('确认吗?', function(index){
                        let orderSns = [];

                        requestData={
                            act:act.bcBespeakOrder_confirm,
                            orderSns:[data.orderSn]
                        };
                        echo().data(requestData).succ(d=>{
                            if (d){
                                t.reload()
                            }
                        }).loading().post();
                        layer.close(index);
                    });

                    break;
                case "nogoods":
                    layer.confirm('确认?', function(index){
                        requestData={
                            act:act.bcBespeakOrder_confirmNoGoods,
                            orderSn:data.orderSn
                        };
                        echo().data(requestData).succ(d=>{
                            if (d){
                                t.reload()
                            }
                        }).loading().post();
                        layer.close(index);
                    });
                    break;
                case "distributive-buyer":

                    echo().data({
                        act:act.bcBespeakOrder_getAssignOrderInfo,
                        orderSn:data.orderSn
                    }).succ(d=>{
                        if(d){
                            router({
                                pid: d,
                                elem: this,
                                table: t,
                            })
                        }
                    }).loading().post()
                    break;
            }

        })
    })
}
