import {echo} from '../golbal/commons.js'
import {act} from '../golbal/static.js'

let option_template =function (d) {
    let id = d.id,pid=d.pid,tb=d.table;
    let data = pid||{
        id:"",
        name:"",
        list:{name:""},
    }

    new Vue({
        el:`.c${id}`,
        delimiters: ['${{', '}}'],
        data:data
    });
let requestdata={};
    layui.use(['form'], function (){
        let form = layui.form;

        form.on('submit(option-template)',function (data) {
            requestdata=data.field;
            let f = $(data.form).attr("flag")=='edit'
            if(f){
                requestdata.act=act.bcTemplate_update
            }else {
                requestdata.act =act.bcTemplate_add;
            }
            let elemnames=[];
            $(data.form).find('input[name=elem-name]').each(function (i,e) {
                elemnames[i]=$(e).val();
            });
            requestdata.elementNames=elemnames;
            echo().data(requestdata).succ(function (d) {
                if(d&&!f){
                    $(`.c${id} input`).val("")
                }
                tb.reload();
            }).loading('加载中').post();
            return false;
        })


    })
}

$(function () {
let $body=$("body"),
    template = `  <div class="layui-form-item">
                                <label class="layui-form-label">选项名称</label>
                                <div class="layui-input-inline">
                                    <input type="text" name="elem-name" lay-verify="title" autocomplete="off" placeholder="请输入名称" class="layui-input">

                                </div>
                            <button class="layui-btn layui-btn-danger  delete-options"><i class="layui-icon">&#xe640;</i> </button>
                     </div>`
    $body.on("click",".cloned .add-options",function () {
        $(this).parents(".layui-form-item").before(template);
    })

    $body.on("click",".cloned .delete-options",function () {
        $(this).parents(".layui-form-item").remove();
    })
});
export default option_template;

