let funcs={
    overview: data=>overview(data),
    classification_of_goods_index_yj:data=>classification_of_goods_index(data),
    classification_of_goods_index_bk:data=>classification_of_good_bk_index(data),
    classification_of_goods_edit:data=> classification_of_goods_edit(data),
    classification_of_goods_administration_yj:data=>classification_of_goods_administration_yj(data),
    classification_of_goods_administration_bk:data=>classification_of_goods_administration_bk(data),
    classification_of_goods_bk_edit:data=>classification_of_goods_edit_bk(data),
    brand_management:data=>brand_management(data),
    stock_order_list:data=>stock_order_list(data),
    stock_order_list_bk:data=>bk_order_list(data),
    stock_order_edit:data=>stock_order_edit(data),
    stock_order_edit_bk:data=>bk_order_edit(data),
    commodity_return_list:data=>commodity_return(data),
    option_template:data=>option_template(data),
    purchasing_agent_management:data=>purchasing_agent_management(data),
    purchasing_agent_management_address:data=>purchasing_agent_management_address(data),
    index_banner_management:data=>index_banner(data),
    index_banner_edit:data=>index_banner_edit(data),
    admin_management:data=>admin_management(data),
    admin_edit:data=>admin_edit(data),
    classification_of_goods_classification:data=>classification_of_goods_classification(data),
    brand_list:data=>brand_list(data),
    special_offer:data=>special_offer(data),
    add_special_offer:data=>special_offer_edit(data),
    free_shipping:data=>free_shipping(data),
    commodity_return_edit:data=>commodity_return_edit(data),
    distributive_buyer:data=>distributive_buyer(data),
    excel_export:data=>excel_export(data),
    delivery_management:data=>delivery_management(data),
    delivery_management_detail:data=>delivery_management_detail(data)
}
let router = function (d) {

    let data = d||{};
    layui.use('element', function() {

        var element = layui.element; //Tab的切换功能，切换事件监听等，需要依赖element模块
        let elem = data.elem;
        //触发事件
        var active = {
            tabAdd: function () {
                //新增一个Tab项
                let $this = $(this);
                let id;
                if($this.attr("flag")=="on"){
                    element.tabChange('taskbar',$(this).attr('tag-id'));
                    return;
                };
                id = new Date().getTime();
                data.id=id;

                $this.attr("tag-id",id);
                let title =($(this).attr('supplementary-title')?$(this).attr('supplementary-title'):"")
                let content;
                let $content = $(`.${$this.attr("data-clone")}`).clone().find(".clone").addClass("cloned").addClass(`c${id}`).attr("type",title).end();
                if($this.attr('btn-type')=='edit'){
                    $content.find(".layui-form").attr("flag",'edit')
                }
                content = $content.html();
                $this.attr("flag","on");
                element.tabAdd('taskbar', {
                    title: $this.text()+title
                    , content: content
                    , id: id,
                }).tabChange('taskbar',$(this).attr('tag-id'));
                let barType =$this.attr('bar-type').replace(/-/g,'_')
                funcs[barType](data);
            }
            ,
        };

           active['tabAdd'].call(elem, elem) ;

    })

}



export default router;