import {act} from "../golbal/static";
import {echo} from "../golbal/commons";

let requestData;
let special_offer_edit=function (d) {
   let data={
        specialOffer:{goods:[],attrs:[]},
    }
    let id = d.id,pid=d.pid,tb=d.table;
    if(pid){
        data.specialOffer=pid;
        data.specialOffer.specialOfferTime=`${pid.startTime} 至 ${pid.endTime}`
    }
   let vue= new Vue({
        el: `.c${id}`,
        delimiters: ['${{', '}}'],
        data:data,
        mounted:function () {
            layui.use(['table' ,'form'], function () {
                let form =layui.form;
                form.on("select(specialOffer-attr-select)",function (data) {
                    $("#specialOffer-price").val($(data.elem).find("option:selected").attr("price"))
                })
                form.on("submit(special-offer-edit-submit)",function (data) {
                    let tips;
                    requestData=data.field;
                    if(Number.parseFloat(data.field.originalPrice)<=Number.parseFloat(data.field.price)){
                        $.tips("特价必须小于原价",4);
                            return false
                    }
                    let f = $(data.elem).parents('.layui-form').eq(0).attr("flag")=='edit'
                    if(f){
                        requestData.act=act.reBcSpecialProduct_update;
                        tips="修改成功"
                    }else {
                        requestData.act=act.reBcSpecialProduct_add;
                        tips="添加成功"
                    }

                    echo().data(requestData).succ(function (d) {
                        if(d){
                            if(!f) {
                                vue.specialOffer = $.cloneObj({goods: [], attrs: []});
                            }
                        $.tips(tips,3)
                            tb.reload();
                        }
                    }).loading().post()
                    return false;
                })
            })

        }
        //vue
    })


};

export default special_offer_edit;
