import {act, imgADD, putExtra, uploadConfig, uuid} from "../golbal/static";
import {echo} from "../golbal/commons";
let requestData,isuploading=false,key='';;
let brand_management =function (d) {
    let data={
        data: {
            id: "",
            name: "",
            priority: "",
            status: 1,
            type: 1,
            img: ""
        }
    },id = d.id,pid=d.pid,tb=d.table;
    if(pid){
        data.data=pid;
    }

   let vue= new Vue({
        el:`.c${id}`,
        delimiters: ['${{', '}}'],
        data:data,
        updated:function () {
        },

    });

    layui.use(['upload','table' ,'form'], function () {
        let upload = layui.upload,form=layui.form;
        var brand_main_img = upload.render({
            elem: `.c${id} #brand-main-img`
            ,auto: false
            , choose: function (obj) {
                echo().data({
                    act:act.upload_upload
                }).succ(d=>{
                    if(d){
                        let f =  $(`.c${id} #brand-main-img-view`).parents(".layui-upload").find("input")[0].files[0]
                        let observer = {
                            next(res){
                            },
                            error(err){
                                $.tips("上传过程出现错误，稍后再试",4)
                            },
                            complete(res){
                                isuploading=false
                                layer.closeAll()
                                $(`.c${id} #brand-main-img-view`).attr('src', `${imgADD.add}${key}${imgADD.brandImg}`);
                            }
                        }
                        key = uuid()
                        let observable = qiniu.upload(f,key , d.uploadToken, putExtra, uploadConfig)
                        observable.subscribe(observer)
                        isuploading=true
                        $.tips(1)
                        //预读本地文件示例，不支持ie8
                        // obj.preview(function (index, file, result) {
                        //     $('#brand-main-img-view').attr('src', result);
                        // });
                    }else {
                        isuploading=false
                    }
                }).err(()=> isuploading=false).post()

            }

        });
        form.render();
        form.on("submit(brand-management-submit)",function (data) {
            requestData=data.field;
            requestData.act=act.bcBrand_add
            let img = $(data.form).find(`#brand-main-img-view`).attr('src');
            if(img){
                requestData.img=img;
            }
            let isSucc = false,tips="";
            let f = $(data.form).attr("flag")=='edit'
            if(f){
                requestData.act=act.bcBrand_update
                tips="修改";
            }else {
                requestData.act=act.bcBrand_add
                tips="添加";
            }

            echo().data(requestData).loading("保存中",2).succ(function (d) {
                if(d){
                    isSucc = true;
                    $.tips(`${tips}成功`,3);
                    if(!f) {
                        key = '';
                        $(data.form).find(`#brand-main-img-view`).attr('src','');
                    }
                    tb.reload();
                }
            }).async()
            if(f)
                return false;
            return isSucc;
        })
    })


}

export default brand_management;