import {requestUrl,act} from "../golbal/static";
import {echo} from "../golbal/commons";
import router from "../plugins/router";
let requestData={} ,tb;
let brand_list =function (d) {
    let id = d.id;
    new Vue({
        el:`.c${id}`,
        delimiters: ['${{', '}}'],
    });

    layui.use(['table', 'form'], function () {
        let table = layui.table
        let form = layui.form;
        requestData.act=act.bcBrand_getList;
        tb= table.render({
            elem: `.c${id} #brand-list-div-table`
            , url: requestUrl.mainUrl
            , method: "post"
            , where: {data: JSON.stringify(requestData), token: requestUrl.token}
            , response: {
                 statusName: 'code'
                , statusCode: 200
                , msgName: 'message'
                , countName: 'total'
                , dataName: 'datalist'
                     }
                , cols: [[ //表头
                {field: 'id', title: 'ID',  sort: true, fixed: 'left'}
                ,{field: 'name', title: '品牌名称',}
                ,{field: 'priority', title: '优先级',sort: true}
                ,{field: 'status', title: '状态',templet: "#brand-list-check", unresize: true}
                ,{field: '', title: '操作', toolbar:"#brand-list-bar"}
            ]]
            ,page: true,
        })
        form.render()
        form.on("switch(brand-list-check)",function (obj) {
            let requestData={};
            requestData.act=act.bcBrand_updateStatus;
            requestData.id=this.value;
            requestData.status=obj.elem.checked?1:0;
            echo().data(requestData).post();
        });
        form.on("submit(brand-list-div-table)",function (data) {
            let requestData;
            requestData=data.field;
            requestData.act=act.bcBrand_getList;
            // console.log(requestData)
            // echo().data(requestData).post()
            // if( data.field.status1&&data.field.status2){
            //     requestData.status=-1;
            // }else {
            //     requestData.status = data.field.status1||data.field.status2;
            // }
            tb.reload({
                where: {
                    data: JSON.stringify(requestData), token: requestUrl.token,
                }
                ,page: {
                    curr: 1 //重新从第 1 页开始
                }
            })
            return false;

        });
        table.on('tool(brand-list-div-table)',function (obj) {
            router({
                pid: obj.data,
                elem:this,
                table:tb,
            })
        })
    })
}
let $body=$("body");
$body.on('click','.cloned .bran-crate-btn',function () {
    router({
        elem:this,
        table:tb,
    })
})
export default brand_list;
